.lk{
  &__grid{
    display: grid;
    grid-template-columns: 20.6875em calc(100% - 21.9375em);
    justify-content: space-between;
    margin-top: 1.875em;
  }
  &__subtitle{
    margin-bottom: 1.25em;
  }
  &._hide-desktop-menu2{
    @media screen and (min-width: 993px){
      .lk-buttons{
        display: none;
      }
    }
  }
  .pagination{
    margin-top: 1.56em;
    &__items{
      justify-content: unset;
    }
  }
}

.lk-nav{
  overflow: hidden;
  border-radius: 0.375em;
  background: #FFF;
  box-shadow: 0px 4px 19px 0px rgba(23, 27, 32, 0.07);
}

.lk-link{
  min-height: 4.6875em;
  display: flex;
  align-items: center;
  padding: 1em 1.875em;
  border-bottom: 1px solid #EDF0F2;
  color: $black2;
  font-weight: 500;
  &._active,
  &:hover{
    background-color: $black5;
    border-color: $black5;
    color: #fff;
    path{
      stroke: #fff;
    }
  }
  &:last-child{
    border-bottom: none;
  }
  &__icon{
    width: 1.4375em;
    flex-shrink: 0;
    margin-right: .625em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
  }
  &__count{
    position: static;
    margin-left: auto;
    transform: unset;
  }
}

.lk-buttons{
  display: grid;
  grid-template-columns: repeat(6,calc(100% / 6 - .94em * 5 / 6));
  gap: .94em;
}

.lk-btn{
  position: relative;
  padding: 1.31em .25em;
  border-radius: 0.3125em;
  border: 1px solid #EFF1F3;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: #1F1E31;
  &__icon{
    height: 3.375em;
    flex-shrink: 0;
    display: block;
    margin-bottom: .94em;
    svg{
      height: 100%;
      width: auto;
    }
  }
  &__title{
    font-size: 0.8125em;
    line-height: 169%;
    text-transform: uppercase;
    font-weight: 500;
  }
  &:hover,
  &._active{
    background: #186FD4;
    border-color: #186FD4;
    color: #fff;
    path{
      stroke: #fff;
    }
  }
  .header-btn__pin{
    right: 1.5em;
    left: unset;
    transform: unset;
  }
}

.orders{
  margin-top: 3.75em;
  @media screen and (min-width: 993px){
    &._mt0-desktop{
      margin-top: 0;
    }
  }
}

.orders-table,
.cart-table{
  table{
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
  }
  td,
  th{

    border-bottom: 1px solid #EDF0F2;
    &:first-child{
      border-left: 1px solid #EDF0F2;
    }
    &:last-child{
      border-right: 1px solid #EDF0F2;
    }
  }
  th{
    border-top: 1px solid #EDF0F2;
    color: #45484A;
    font-weight: 700;
    text-align: left;
    text-transform: uppercase;
    &:first-child{
      border-radius: 0.5em 0 0 0;
    }
    &:last-child{
      border-radius: 0 0.5em 0 0;
    }
    span{
      font-size: 0.875em;
      line-height: 171%;
    }
  }
  tbody tr:last-child{
    td{
      &:first-child{
        border-radius:  0 0 0 0.5em;
      }
      &:last-child{
        border-radius: 0 0 0.5em 0;
      }
    }
  }
}

.orders-table{
  td,th{
    padding: 1.25em;
    &:nth-child(4){
      width: 8.5em;
    }
      &:last-child{
      width: 4.375em;
      padding-left: 0;
    }
  }
  td{
    color: $black2;
  }
}

.order-item{
  &__price{
    color: #003B73;
  }
  &__link{
    width: 3.125em;
    height: 3.125em;
    span{
      display: none;
    }
    svg{
      width: .3125em;
      height: auto;
    }
    &:hover{
      path{
        stroke: #fff;
      }
    }
  }
}

.order-badge{
  display: inline-flex;
  align-items: center;
  height: 2.125em;
  min-width: 6.25em;
  padding: 0 .75em;
  color: #FB5A00;
  text-transform: uppercase;
  font-weight: 500;
  border: 1px solid #FF6915;
  border-radius: .5em;
  &._green{
    color: #11A342;
    border-color: #11A342;
    &:before{
      background-color: #11A342;
    }
  }
  &._red{
    color: #F81D2A;
    border-color: #F81D2A;
    &:before{
      background-color: #F81D2A;
    }
  }
  &:before{
    content: '';
    flex-shrink: 0;
    width: 0.3125em;
    height: 0.3125em;
    border-radius: 50%;
    margin-right: .56em;
    background-color: #FF6915;
  }
  span{
    font-size: .6875em;
    line-height: 120%;
  }
}

.account-form{
  padding: 2.5em;
  border-radius: 0.5em;
  border: 1px solid #EDF0F2;
  width: 42.5625em;
  max-width: 100%;
  @include grid100gap(1.25em);
  &._pass{
    width: 34em;
  }
  &._address{
    width: 64.625em;
    gap: 1.56em;
    @media screen and (min-width: 577px){
      grid-template-columns: repeat(2,calc(50% - .78em));
    }
    .account-form__btn{
      display: inline-flex;
      padding: 0 2em;
      width: auto;
      margin-right: auto;
    }
  }
  &__btn{
    width: 100%;
    height: 4em;
  }
  ._full{
    grid-column: 1/-1;
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .78em));
    gap: 1.56em;
  }
}

.address-card{
  border-radius: 0.5em;
  border: 1px solid #F1F1F1;
  overflow: hidden;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 3.9375em;
  }
  &__name{
    padding: 0 1.875em;
    color: #121214;
  }
  &__badge{
    height: 4em;
    padding: 0 1.44em;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font-weight: 500;
    border-radius: 0;
    pointer-events: none;
    span{
      font-size: .8125em;
    }
    svg{
      width: 1.1875em;
      height: auto;
      margin-right: .625em;
    }
  }
  &__center{
    padding: .81em 1.875em 1em;
    min-height: 5.81em;
    display: flex;
    justify-content: space-between;
    color: #3B3B3E;
    .text15{
      line-height: 167%;
      font-weight: 500;
    }
    .text14{
      color: #87878D;
      line-height: 178%;
    }
  }
  &__address{
    width: 17.625em;
  }
  &__nav{
    display: inline-flex;
    align-items: center;
    padding: 1.12em 1.875em;
    border-radius: 0 0.5em 0 0;
    border: 1px solid #F1F1F1;
  }
  &__btn{
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font-weight: 500;
    height: 1.4375em;
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    &:not(:last-child){
      &:after{
        content: '';
        flex-shrink: 0;
        width: 1px;
        height: 1.25em;
        border-right: 1px dashed #F1F1F1;
        margin: 0 1.875em;
        pointer-events: none;
      }
    }
    svg{
      height: 1.1875em;
      width: auto;
      margin-right: .56em;
      flex-shrink: 0;
    }
    span{
      font-size: 0.6875em;
      line-height: 209%;
    }
  }
  &__new{
    padding: .875em 1.875em 1.875em;
    display: flex;
  }
  &__add{
    display: flex;
    align-items: center;
    color: #186FD4;
    &:hover{
      color: $black;
    }
    &-icon{
      background: #F9F9F9;
      width: 3.875em;
      height: 3.875em;
      border-radius: 50%;
      flex-shrink: 0;
      @include center;
      margin-right: 1.06em;
      svg{
        width: 1em;
        height: auto;
      }
    }
    .text16{

    }
  }
}

.order-page{
  margin-bottom: 2em;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__back{
    height: 2.25em;
    padding: 0 .94em;
    text-transform: uppercase;
    white-space: nowrap;
    svg{
      width: .375em;
      height: auto;
      margin-right: .625em;
    }
    span{
      font-size: .75em;
    }
    &:hover{
      path{
        stroke: #fff;
      }
    }
  }
  &__info{
    display: flex;
    align-items: center;
    .text14{
      color: #646464;
      b{
        color: #141414;
        font-weight: 500;
      }
    }
    .order-badge{
      margin-left: .625em;
    }
  }
}

.order-detail{
  margin-top: 2em;
  border: 1px solid #EDF0F2;
  border-radius: 0.5em;
  &__top{
    padding: 1.25em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    .text18{
      font-weight: 700;
      line-height: 111%;
      color: #1F1E31;
    }
  }
}

.detail-item{
  display: flex;
  justify-content: space-between;
  padding: .94em 1.25em;
  border-top: 1px solid #F1F1F1;
  &__title{
    display: flex;
  }
  &__title2{
    color: #1F1E31;
  }
  &__title3{
    color: #494950;
    text-align: right;
    @media screen and (min-width: 993px){
      br{
        display: none;
      }
    }
  }
  &__title2,
  &__title3{
    font-size: 1em;
    line-height: 150%;
  }
  &__name{
    font-size: 1em;
    line-height: 162%;
    font-weight: 500;
  }
  &__count{
    margin-left: .94em;
    color: #7A7A7E;
    .text14{
      line-height: 129%;
    }
  }
  &__price{
    color: #003B73;
    white-space: nowrap;
    margin-left: .94em;
    .text18{
      line-height: 122%;
    }
    .text22{
      line-height: 100%;
    }
  }
  &__all{
    font-size: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .lk{
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
    }
    .mob-filter__btn{
      display: none;
    }
    &__subtitle{
      margin-bottom: .94em;
    }
    .pagination{
      margin-top: 1.875em;
    }
    &._hide-desktop-menu2{
      .lk-buttons{
        grid-template-columns: repeat(6,calc(100% / 6 - .625em * 5 / 6));
        gap: .625em;
      }
      .lk-btn{
        padding: 1.06em .625em;
        &__icon{
          height: 1.9375em;
          margin-bottom: .69em;
        }
        &__title{
          line-height: 131%;
        }
        .header-btn__pin{
          top: .72em;
          transform: translateX(1.25em);
        }
      }
    }
  }

  .lk-nav{
    width: calc(100% + 2.76em);
    margin-left: -1.38em;
    border-radius: 0;
    background-color: transparent;
    box-shadow: unset;
  }

  .lk-buttons{
    grid-template-columns: repeat(3,calc(100% / 3 - .94em * 2 / 3));
  }

  .lk-btn{
    .header-btn__pin{
      right: 50%;
      top: 1.44em;
      transform: translateX(1.75em);
    }
  }

  .orders{
    margin-top: 3.125em;
  }

  .account-form{
    width: 100%;
    padding: 1.25em .94em;
    &__wrap{
      margin-top: 2.5em;
    }
    ._full-tablet{
      grid-column: 1/-1;
    }
    &._address{
      .account-form__btn{
        width: 100%;
        grid-column: 1/-1;
      }
    }
  }

  .address{
    margin-top: 2.5em;
  }

  .address-card{
    &__new{
      padding-top: 1.12em;
      justify-content: center;
    }
  }

  .order-page{
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 576px){
  .lk{
    &._show-mob-menu{
      .mob-filter__btn{
        display: flex;
      }
    }
    &._hide-mob-menu2{
      .lk-buttons{
        display: none;
      }
    }
    .pagination{
      margin-top: 2.5em;
      &__items{
        justify-content: center;
      }
    }
  }

  .lk-buttons{
    grid-template-columns: repeat(2,calc(50% - .47em));
  }

  .orders{
    margin-top: 1.875em;
    .lk__subtitle{
      margin-bottom: .625em;
    }
  }

  .orders-table{
    table{
      display: block;
    }
    thead{
      display: none;
    }
    tbody{
      @include grid100gap(.94em);
    }
    tr{
      border-radius: 0.5em;
      border: 1px solid #EAEAEA;
      @include grid100gap(.3125em);
      padding: .94em;
    }
    td{
      border: none !important;
      padding: 0;
      width: 100% !important;
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: #1F1E31;
      &[data-title]{
        min-height: 1.875em;
        &:before{
          content: attr(data-title);
          letter-spacing: 0.0075rem;
          text-transform: uppercase;
          font-size: .75em;
          font-weight: 500;
        }
      }
    }
    .text16{
      font-size: .8125em;
      font-weight: 500;
    }
  }

  .order-item{
    &__link{
      width: 100%;
      height: 2.625em;
      svg{
        display: none;
      }
      span{
        display: block;
        font-size: .9375em;
      }
    }
    &__price{
      font-size: 1.0625em;
    }
  }

  .account-form{
    &__wrap{
      margin-top: 1.875em;
    }
    &__fields{
      grid-template-columns: 100%;
    }
  }

  .address{
    margin-top: 1.875em;
  }

  .address-card{
    &__top{
      flex-direction: column;
    }
    &__badge{
      order: -1;
    }
    &__name{
      padding: .81em 0 0;
      text-align: center;
    }
    &__center{
      padding: 1.12em 1.875em 1.875em;
      @include grid100gap(1.44em);
    }
    &__nav{
      display: flex;
      width: 100%;
      justify-content: center;
    }
    &__btn{
      &:not(:last-child){
        &:after{
          margin: 0 .9375em;
          border: none;
          width: 0;
        }
      }
    }
  }

  .order-page{
    margin-top: 1.875em;
    &__info{
      display: block;
      .order-badge{
        margin: .25em 0 0;
      }
    }
  }

  .detail-item{
    &__title{
      display: block;
    }
    &__name{
      display: inline;
    }
    &__count{
      display: inline;
      margin: 0;
    }
  }
}
