.footer{
  overflow: hidden;
  padding: 2.5em 0 1.25em;
  background: #F3F4F5;
}


.footer-top{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-bottom: 2.56em;
  border-bottom: 1px solid #DCE1E7;
}

.footer-logo{
  display: flex;
  align-items: center;
  margin-right: auto;
  &__img{
    width: 13.4375em;
    flex-shrink: 0;
    display: block;
    margin-right: 3.19em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    color: #35383A;
  }
}

.footer-contacts{
  &__btn{
    width: 13.375em;
    text-transform: uppercase;
  }
  &__phone{
    margin-right: 2.5em;
    .text13{
      margin-top: .4em;
      color: #4E5760;
      white-space: nowrap;
    }
  }
  &__email{
    margin-right: 7.5em;
    .text13{
      margin-bottom: .2em;
    }
    .text14{
      color: #117FE3;
      text-decoration: underline;
      font-weight: 500;
    }
  }
  &__inn{
    margin-right: 9em;
    color: #35383A;
    .text13{
      line-height: 146%;
    }
  }
}

.footer-nav{
  margin-top: 2.56em;
  display: flex;
  justify-content: space-between;
}

.footer-group{
  &._type1{
    max-width: 24.25em;
  }
  &._type2{
    max-width: 63.5625em;
    flex-grow: 1;
    .footer-group__links{
      column-count: 3;
      column-gap: 6.25em;
      height: 16em;
    }
  }
  &__show{
    color: #333436;
    .text18{
      line-height: 144%;
    }
  }
  &__hidden{
    margin-top: 1.875em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__links{
    column-count: 2;
    column-gap: 2.5em;
  }
  &__link{
    display: block;
    width: 100%;
    font-size: 1em;
    line-height: 138%;
    color: #404244;
    @include lastM(0 0 1.5em);
  }
}

.footer-bottom{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 2.56em;
  padding-top: 1.56em;
  border-top: 1px solid #DCE1E7;
}

.footer-pay{
  display: flex;
  align-items: center;
  &__title{
    margin-right: 6.31em;
    color: #979DA3;
    .text14{
      line-height: 129%;
    }
  }
  &__items{
    display: flex;
    align-items: center;
  }
  &__item{
    opacity: .2;
    transition: all .3s;
    @include lastM(0 2.81em 0 0);
    &:hover{
      opacity: 1;
    }
    img{
      max-width: 4.8125em;
      max-height: 1.5em;
      display: block;
    }
  }
}

.footer-subscribe{
  flex-grow: 1;
  max-width: 43.25em;
  display: flex;
  align-items: center;
  &__title{
    margin-right: 2.25em;
    color: #4C5054;
    font-weight: 500;
    max-width: 14em;
    flex-shrink: 0;
    .text17{
      line-height: 135%;
    }
  }
  &__field{
    flex-grow: 1;
    height: 3.5em;
    position: relative;
    input{
      width: 100%;
      height: 100%;
      font-size: 0.875em;
      line-height: 120%;
      border: 1px solid #fff;
      border-radius: 0.57em;
      background: #FFF;
      padding-left: 1.42em;
      padding-right: 3em;
      &::placeholder{
        color: #6B6F73;
      }
      &:focus{
        border-color: $primary;
      }
    }
  }
  &__btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 1.0625em;
    right: 1.25em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.footer-info{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 1.81em;
  padding-top: 1.25em;
  border-top: 1px solid #DCE1E7;
  &__c{
    max-width: 33.625em;
    color: #81868B;
    .text14{
      line-height: 186%;
    }
  }
  &__right{
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 43.25em;
  }
  &__by{
    width: 8.0625em;
    display: block;
    &:not(:hover){
      opacity: .8;
    }
    img{
      width: 100%;
      display: block;
    }
  }
  &__conf{
    text-decoration: underline;
    line-height: 186%;
    color: #444648;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: .94em 0 1.25em;
  }

  .footer-top{
    padding-bottom: 1.125em;
  }

  .footer-logo{
    &__img{
      margin: 0;
      width: 9.9375em;
    }
    &__text{
      display: none;
    }
  }

  .footer-contacts{
    &__inn{
      display: none;
    }
    &__btn{
      width: 9.4375em;
      padding: 0;
      span{
        font-size: .6875em;
      }
    }
    &__phone{
      margin-right: .81em;
      text-align: right;
      .text21{
        font-size: 1.125em;
      }
      .text13{
        margin-top: 0;
      }
    }
    &__email{
      margin-right: 3.13em;
    }
  }

  .footer-nav{
    margin-top: 1.25em;
  }

  .footer-group{
    &._type1{
      max-width: 17.625em;
    }
    &._type2{
      max-width: 25.1875em;
      .footer-group__links{
        column-count: 2;
        column-gap: 2em;
        height: auto;
      }
    }
    &__hidden{
      margin-top: .94em;
    }
    &__links{
      column-gap: 1.5em;
    }
    &__link{
      font-size: .875em;
      line-height: 129%;
      @include lastM(0 0 .8em);
    }
  }

  .footer-bottom{
    margin-top: .94em;
    padding-top: 1.25em;
    display: block;
  }

  .footer-pay{
    display: block;
    &__title{
      display: none;
    }
    &__items{
      justify-content: space-between;
    }
  }

  .footer-subscribe{
    max-width: 100%;
    margin-top: 1.25em;
  }

  .footer-info{
    margin-top: 1.25em;
    padding-top: 1.06em;
    display: block;
    &__right{
      margin-top: .625em;
    }
    &__c{
      max-width: 100%;
      .text14{
        font-size: 0.8125em;
        line-height: 177%;
      }
    }
    &__conf{
      order: -1;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding-bottom: 1.5em;
  }

  .footer-top{
    display: grid;
    grid-template-columns: calc(100% - 10.2475em) 9.4375em;
    gap: 1.125em .81em;
  }

  .footer-contacts{
    &__btn{
      width: 100%;
    }
    &__phone{
      margin: 0;
    }
    &__email{
      margin: 0;
    }
  }

  .footer-nav{
    margin: 0;
    display: block;
  }

  .footer-group{
    max-width: 100% !important;
    &__show{
      min-height: 2.875em;
      padding: .62em 0;
      border-bottom: 1px solid #DCE1E7;
      display: flex;
      align-items: center;
      justify-content: space-between;
      &:after{
        content: '';
        width: .5em;
        height: .5em;
        flex-shrink: 0;
        transition: all .3s;
        background-image: url(../img/arrow-bottom.svg);
        @include bgImgSize(100%);
      }
      &._active{
        &:after{
          transform: rotate(-180deg);
        }
      }
    }
    &__hidden{
      margin: 0;
      padding: .94em 0;
      border-bottom: 1px solid #DCE1E7;
      display: none;
    }
  }

  .footer-bottom{
    margin-top: 1.125em;
    padding-top: 0;
    border: none;
  }

  .footer-pay{
    &__items{
      flex-wrap: wrap;
      justify-content: center;
      width: calc(100% + 1.44em);
      margin-top: -1.25em;
    }
    &__item{
      margin: 1.25em 1.44em 0 0 !important;
    }
  }

  .footer-subscribe{
    margin-top: 1.19em;
    display: block;
    &__title{
      max-width: 100%;
      margin: 0 0 .625em;
      text-align: center;
      .text17{
        font-size: .9375em;
        line-height: 143%;
      }
    }
  }

  .footer-info{
    margin-top: 1.13em;
    &__right{
      flex-direction: column;
      align-items: center;
    }
    &__by{
      margin-top: .625em;
    }
  }
}
