@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
._img-cover{
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    border-radius: inherit;
  }
}

.swiper-button{
  transition: all .3s;
  border-radius: 50%;
  background: #FFF;
  box-shadow: 0 .25em 1.5625em 0 rgba(0, 0, 0, 0.05);
  width: 1.875em;
  height: 1.875em;
  svg{
    width: .375em;
    height: auto;
  }

  &:hover{
    background: $black5;
    path{
      fill: #fff;
    }
  }
  @media screen and (min-width: 993px){
    &._big{
      width: 3.75em;
      height: 3.75em;
      svg{
        width: .5625em;
      }
    }
  }
}

._section{
  padding: 2.8125em 0;
  &._first{
    padding-top: 0;
  }
  &._last{
    padding-bottom: 6.875em;
  }
}

._section2{
  padding: 3.125em 0;
  margin: 2.8125em 0;
  background: #F9FAFB;
}

._grid-column4,
._grid-column5{
  display: grid;
  gap: 1.25em;
}
._grid-column4{
  grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
}
._grid-column5{
  grid-template-columns: repeat(5,calc(20% - 1em));
}


@media screen and (max-width: 992px){
  ._section{
    padding: 1.5625em 0;
    &._last{
      padding-bottom: 5em;
    }
  }

  ._section2{
    padding: 1.875em 0;
    margin: 1.5625em 0;
  }

  ._grid-column2-tablet{
    grid-template-columns: repeat(2,calc(50% - .47em));
    gap: .94em;
  }
}


@media screen and (max-width: 576px){
  ._grid-column1-mob{
    grid-template-columns: 100%;
  }
}
