.promos{
  &__items{
    margin-top: 1.25em;
  }
  @media screen and (min-width: 993px){
    .subscribe{
      margin-top: 3.75em;
      grid-template-columns: 20.625em calc(100% - 45.1875em) 20.8125em;
      gap: 1.875em;
      align-items: center;
      &__top{
        text-align: left;
        gap: .31em;
      }
      &__wrap{
        grid-template-columns: calc(100% - 13.69em) 12.75em;
        gap: .94em;
      }
    }
  }
}

.promo-card2{
  .promo-card__img{
    padding-bottom: 56.4%;
  }
  .promo-card__content{
    justify-content: flex-end;
    padding: 1.875em 2.0625em;
  }
  &__bottom{
    margin-top: .94em;
  }
  &__nav{
    display: flex;
    margin-top: .31em;
  }
  &__link{
    color: #186FD4;
    font-size: 1em;
    font-weight: 500;
    line-height: 162%;
    text-decoration-line: underline;
  }
}

.promo-item{
  .article{
    margin: 0;
    &__top{
      margin: 0;
    }
  }
}

.promocode{
  margin-top: 1.25em;
}

.promocode-item{
  margin-top: 1.25em;
  display: inline-flex;
  align-items: center;
  padding: 0 .94em;
  border-radius: 0.4375em;
  border: 1px dashed #BDCDE0;
  height: 2.875em;
  &__title{
    font-size: 0.875em;
    line-height: 186%;
    color: #186FD4;
    font-weight: 500;
  }
  .copy-btn{
    margin-left: .625em;
    flex-shrink: 0;
  }
}

.copy-btn{
  width: 1.125em;
  svg{
    width: 100%;
    height: auto;
  }
  &:hover{
    path{
      stroke: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .promos{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
    .subscribe{
      margin-top: 3.125em;
    }
  }

  .promo-card2{
    .promo-card__content{
      padding: 1.875em 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .promos{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .promo-card2{
    .promo-card__content{
      padding: 1.25em;
    }
  }
}
