.contacts{
  &__grid{
    margin-top: 1.875em;
    position: relative;
  }
}

.map{
  height: 38.1875em;
  width: 87.0625em;
  max-width: 100%;
  border-radius: 0.625em;
  border: 1px solid #E9ECEF;
  overflow: hidden;
}

.contacts-card{
  width: 21.9375em;
  border-radius: 0.375em;
  background: #FFF;
  box-shadow: 0px 4px 19px 0px rgba(23, 27, 32, 0.07);
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  position: absolute;
  z-index: 3;
  padding: 1.875em;
  @include grid100gap(1.4375em);
  &__btn{
    height: 3.875em;
  }
}

.contact-item{
  display: grid;
  grid-template-columns: 1.6875em calc(100% - 2.3125em);
  gap: .625em;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    grid-column: 2/3;
  }
  &__title{
    min-height: 1.6875em;
    display: flex;
    align-items: center;
    color: #2F3134;
  }
  &__desc{
    margin-top: .625em;
    color: #494D52;
    .text15{
      line-height: 140%;
    }
  }
  &__phone{
    color: #3B3C3F;
    line-height: 123%;
    font-weight: 500;
  }
  &__email{
    color: #186FD4;
    text-decoration: underline;
  }
}

.contact-places{
  display: grid;
  grid-template-columns: calc(100% - 22em) 20.5em;
  gap: 1.5em;
  margin-top: 3.125em;
  &__items{
    display: grid;
  }
}

.place-card,
.region-card{
  .text15{
    color: #494D52;
    line-height: 140%;
  }
  &__link{
    color: #2F3134;
    line-height: 116%;
    font-weight: 500;
    white-space: nowrap;
    &:hover{
      color: $primary;
    }
  }
}

.place-card{
  border-radius: 0.5em;
  border: 1px solid #F2F4F6;
  padding: 1.625em 1.5em;
  &__desc{
    margin-top: .94em;
  }
}

.requisites{
  padding: 1.875em 1.69em 1.25em 1.25em;
  background: #F7F9FC;
  color: #2B2E31;
  .text16{
    line-height: 131%;
    margin-bottom: .3125em;
  }
}

.regions{
  margin-top: 3.125em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 4.44em * 5 / 6));
    gap: 4.44em;
    margin-top: 1.56em;
  }
}

.region-card{
  border-right: 1px solid #E5E9ED;
  @media screen and (min-width: 993px){
    &:nth-child(6n){
      border-right: none;
    }
  }
  &__item{
    margin-top: .44em;
  }
  &__mail{
    color: #186FD4;
    line-height: 140%;
    text-decoration: underline;
  }
}

.consult{
  margin-top: 2.5em;
  padding: 3.75em 0;
  background: #F9FAFB;
  .text33{
    text-align: center;
    br{
      display: none;
    }
  }
}

.consult-form{
  max-width: 100%;
  width: 46em;
  margin: 3.3125em auto 0;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .9375em));
  gap: 1.875em;
  ._full{
    grid-column: 1/-1;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__btn{
    height: 3.875em;
    width: 13.0625em;
    flex-shrink: 0;
    margin-right: 1.875em;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    &__grid{
      margin-top: 1.25em;
      overflow: hidden;
      @include fullScreenTablet2;
    }
  }

  .map{
    width: 120%;
    max-width: unset;
    margin-left: -20%;
    height: 32.875em;
    border-radius: 0;
  }

  .contacts-card{
    right: 1.56em;
    width: 20.625em;
    padding: .94em;
  }

  .contact-places{
    grid-template-columns: 100%;
    gap: .94em;
    margin-top: 1.875em;
  }

  .regions{
    margin-top: 2.5em;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 4.44em * 2 / 3));
    }
  }

  .region-card{
    @media screen and (min-width: 993px){
      &:nth-child(3n){
        border-right: none;
      }
    }
  }

  .consult{
    margin-top: 1em;
    padding: 2.5em 0;
    .text33{
      br{
        display: unset;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__grid{
      @include fullScreenMob2;
    }
  }

  .map{
    height: 38.1875em;
    width: 100%;
    margin: 0;
  }

  .contacts-card{
    position: relative;
    transform: unset;
    right: unset;
    margin: -5.6875em auto 0;
    width: calc(100% - 1.875em);
  }

  .regions{
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .region-card{
    border-right: none;
  }

  .consult-form{
    grid-template-columns: 100%;
    &__nav{
      flex-direction: column;
    }
    &__btn{
      margin: 0 auto 1.25em;
    }
  }
}
