.catalog{
  &__top{
    display: flex;
    align-items: center;
    &-left{
      display: flex;
    }
    .brand-card{
      width: 7.625em;
      height: 3.2em;
      margin-left: 1.75em;
    }
  }
  &__count{
    color: #6C7177;
    align-self: flex-end;
    margin-left: 1.13em;
    padding-bottom: .57em;
  }
  &__inner{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: 20.4375em calc(100% - 21.9375em);
    gap: 1.5em;
  }
  &__items{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.0625em * 3 / 4));
    gap: 1.0625em;
  }
  .catalog-promo{
    margin-top: 2.5em;
  }
}

.filter{
  &__top,
  &__bg{
    display: none;
  }
  &__groups{
    @include grid100gap(1.25em);
  }
  &__nav{
    margin-top: 1.875em;
  }
  &__btn{
    width: 100%;
    height: 3.875em;
  }
  &__links{
    @include grid100gap(1.125em);
    .blog-nav__link{
      min-height: unset;
      border: none !important;
      &-title{
        text-transform: unset;
        font-size: .9375em;
        color: $black2;
      }
    }
  }
}

.filter-group{
  &._active{
    .filter-group__toggle{
      svg{
        transform: rotate(-180deg);
      }
    }
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-size: 1.125em;
    line-height: 144%;
    font-weight: 500;
    color: #414446;
  }
  &__toggle{
    width: .5em;
    position: relative;
    &:hover{
      path{
        fill: $primary;
      }
    }
    &:before{
      content: '';
      @include centerAbsolute;
      width: 300%;
      height: 300%;
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__bottom{
    padding: 1.125em 0 1.25em;
    display: none;
    .catalog__search{
      margin-bottom: .81em;
    }
  }
  &__checkboxes{
    @include grid100gap(.94em);
    &:not(._show-all){
      .m-check:nth-child(5)~.m-check{
        display: none;
      }
    }
    &._show-all{
      .more-checkbox__wrap{
        display: none;
      }
    }
  }
}

.filter-slider{
  &__item{
    min-height: .9375em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: #DFE0E2;
    border: none;
    box-shadow: none;
    height: .125em;
    border-radius: 0;
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      background-color: #fff;
      border: 2px solid $primary;
      width: 1.1875em;
      height: 1.1875em;
      top: -.59375em !important;
      right: -.59375em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin: 0 0 1em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5625em));
    gap: 1.125em;
  }
  .fg{
    input{
      height: 3.25em;
      padding-left: 2.25em;
      color: #515356;
    }
    span{
      position: absolute;
      top: 50%;
      left: 1em;
      transform: translateY(-50%);
      font-size: .9375em;
      line-height: 120%;
      color: #A7AAAE;
    }
  }
}

._line-btn{
  font-size: 0.9375em;
  line-height: 120%;
  font-weight: 700;
  border-bottom: 1px solid rgba(17, 127, 227, 0.60);
  color: #117FE3;
}

.more-checkbox{
  &:hover{
    border-color: transparent;
  }
  &__wrap{
    display: flex;
  }
}

.mob-filter{
  &__btn{
    display: none;
  }
}

.catalog-promo{
  @include grid100gap(2.5em);
  .promo-card{

    &__img{
      padding-bottom: 82.4%;
    }
    &__content{
      padding: 1.56em;
    }
  }
}

.filter2{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sort{
  display: flex;
  align-items: center;
  .fg{
    margin-left: .625em;
    width: 15.125em;
  }
  .nice-select{
    height: 2.875em;
    .option{
      height: 2.875em;
    }
  }
}

.count-pagination{
  display: flex;
  align-items: center;
  &__items{
    display: flex;
    align-items: center;
    margin-left: .625em;
  }
  &__btn{
    margin-left: .625em;
    width: 3.125em;
    height: 2.8125em;
    @include center;
    border-radius: 0.375em;
    border: 1px solid #EEE;
    color: #414141;
    span{
      font-size: 0.9375em;
    }
    &:hover,
    &._active{
      background: #F6F8FB;
      border-color: #F6F8FB;
      color: #186FD4;
    }
  }
}

.selected-filter{
  display: flex;
  flex-wrap: wrap;
  margin-top: 1.375em;
  width: calc(100% + .625em);
  &__item{
    flex-shrink: 0;
    margin: .625em .625em 0 0;
    border-radius: .375rem;
    border: 1px solid #F2F4F5;
    min-height: 2.81225em;
    display: flex;
    align-items: center;
    padding: .25em 1.25em;
    color: #5B5B5C;
    font-weight: 500;
    &-remove{
      width: .625em;
      flex-shrink: 0;
      margin-left: .625em;
      svg{
        width: 100%;
        height: auto;
      }
      &:hover{
        path{
          opacity: 1;
        }
      }
    }
  }
  &__clear{
    flex-shrink: 0;
    background-color: #F6F8FB;
    border-color: #F6F8FB;
    color: #117FE3;
  }
}

.pagination{
  margin-top: 5.625em;
  &__items{
    display: flex;
    justify-content: center;
  }
  &__item{
    height: 3.5em;
    min-width: 3.5em;
    padding: 0 1.25em;
    border-radius: .3125em;
    border: 1px solid #DFDFDF;
    @include center;
    margin: 0 .3125em;
    font-weight: 400;
    color: #1B1B1B;
    svg{
      width: 1.4375em;
      height: auto;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $black5;
      border-color: $black5;
      path{
        fill: #fff;
      }
    }
    &._btn{
      svg{
        &:first-child{
          margin-right: .625em;
        }
        &:last-child{
          margin-left: .625em;
        }
      }
    }
  }
}

.info{
  margin-top: 5em;
  .text{
    margin-top: .88em;
  }
  .more-text{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .catalog{
    &__inner{
      margin-top: 1.25em;
      grid-template-columns: 100%;
      gap: 0;
    }
    &__top{
      .text48{
        font-size: 1.875em;
      }
    }
    &__count{
      display: none;
    }
    &__items{
      margin-top: 1.25em;
      grid-template-columns: repeat(3,calc(100% / 3 - 2.125em / 3));
    }
    .product-card{
      padding: 0;
    }
    .catalog-promo{
      display: none;
    }
  }

  .catalog-promo{
    .promo-card{
      .text22{
        font-size: 1.375em;
      }
      .m-badge{
        margin-top: .44em;
      }
    }
  }

  .mob-filter{
    &__btn{
      @include center;
      height: 3.375em;
      border-top: 1px solid #DFDFDF;
      border-bottom: 1px solid #DFDFDF;
      @include fullScreenTablet;
      color: #2C333D;
      svg{
        width: 1.25em;
        height: auto;
        margin-right: .625em;
      }
      span{
        font-size: 1em;
        line-height: 120%;
      }
    }
  }

  .filter{
    padding: 1.56em 1.38em;
    border-radius: 0;
    height: $fullHeight;
    overflow-y: auto;
    width: 23.185em;
    top: 0;
    left: 0;
    position: fixed;
    z-index: 20;
    background-color: #fff;
    border: none;
    margin: 0;
    transform: translateX(-120%);
    transition: all .3s;
    &._active{
      transform: translateX(0%);
      &~.filter__bg{
        display: block;
      }
    }
    .menu-top{
      padding: 0 0 1.25em;
    }
    &__close{
      margin-left: auto;
      width: 1.625em;
      height: 1.625em;
      @include center;
      svg{
        width: .5em;
        height: auto;
      }
      &:hover{
        path{
          stroke: #fff;
        }
      }
    }
  }

  .filter2{
    margin-top: 1.25em;
  }

  .sort{
    .fg{
      width: 13.625em;
    }
  }

  .count-pagination{
    &__btn{
      margin-left: .3125em;
    }
  }

  .selected-filter{
    margin-top: .625em;
  }

  .pagination{
    margin-top: 3.125em;
    ._hide-tablet{
      display: none;
    }
  }

  .info{
    margin-top: 3.12em;
    .more-text{
      display: flex;
      justify-content: center;
      margin-top: .875em;
    }
    .text{
      position: relative;
      &:not(._show-all){
        height: 21.8125em;
        overflow: hidden;
        &:before{
          z-index: 2;
          content: '';
          position: absolute;
          width: 100%;
          height: 4.5625em;
          bottom: 0;
          left: 0;
          background: linear-gradient(180deg, rgba(255, 255, 255, 0.00) 0%, #FFF 100%);
        }
      }
      &._show-all{
        &~.more-text{
          display: none;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1.56em 1em;
    }
  }

  .mob-filter{
    &__btn{
      @include fullScreenMob;
    }
  }

  .filter{
    width: 20.6875em;
  }

  .filter2{
    margin-top: .94em;
    display: block;
  }

  .sort{
    .fg{
      flex-grow: 1;
    }
  }

  .count-pagination{
    display: none;
  }

  ._small-cards{
    .product-card{
      &__img{
        height: 7.1875em;
      }
      &__nums,
      &__title,
      &__articul{
        margin-top: .3125em;
      }
      &__title{
        height: 3.8125em;
        .text18{
          font-size: .9375em;
          line-height: 133%;
        }
      }
      &__nums{
        flex-wrap: wrap;
      }
      &__price{
        width: 100%;
      }
      .cart-btn{
        min-width: unset;
        width: 3.6875em;
        position: relative;
        &:before{
          content: '';
          @include centerAbsolute;
          width: 1em;
          height: 1em;
          background-image: url(../img/check.svg);
          @include bgImgSize(100%);
          opacity: 0;
        }
        span{
          display: none !important;
        }
        img{
          margin: 0;
        }
        &._added{
          background: $primary;
          &:before{
            opacity: 1;
          }
        }
      }
    }
  }

  .pagination{
    margin-top: 2.5em;
    &__item{
      margin: 0 .1875em;
      &._btn{
        padding: 0;
        span{
          display: none;
        }
        svg{
          margin: 0 !important;
        }
      }
    }
  }

  .info{
    .text {
      &:not(._show-all) {
        height: 40.875em;
      }
    }
  }
}
