.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    padding: 1em 0;
    text-align: center;
    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog{
        text-align: left;
        vertical-align: middle;
    }

    &__bg {
        background: rgba(#011120, 0.80);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        border-radius: .5em;
        background: #fff;
        width: 38.625em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        padding: 2.5em;
        display: inline-grid;
        grid-template-columns: 100%;
        gap: 1.56em;
    }
    &__close {
        z-index: 10;
        border-top: 0;
        background-color: transparent;
        width: 2.125em;
        display: flex;
        align-items: center;
        position: fixed;
        top: 3.125em;
        right: 3.125em;
        svg {
            width: 100%;
            height: auto;
        }
    }
    &__top{
        color: #17212C;
        text-align: center;
        @include grid100gap(.94em);
    }
    &__desc{
        font-size: 1.3125em;
        line-height: 148%;
        color: $black2;
        font-weight: 500;
    }
}

.modal-form{
    @include grid100gap(1.56em);
    .m-check{
        label span{
            font-size: .8125em;
        }
    }
    &__btn{
        width: 100%;
        height: 4.1875em;
    }
}

.cookie-modal{
    position: fixed;
    bottom: 0;
    left: 0;
    background: #FFF;
    box-shadow: 0px -4px 33px 0px rgba(0, 0, 0, 0.10);
    z-index: 20;
    padding: 1.25em 0;
    width: 100%;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &._active {
        opacity: 1;
        pointer-events: unset;
    }
    &__inner{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 2.44em;
    }
    &__content{
        @include grid100gap(.5em);
        max-width: 61.5em;
    }
    &__title{
        font-size: 1.125em;
        line-height: 144%;
        font-weight: 500;
        color: #3B3C3F;
    }
    &__desc{
        font-size: .875em;
        line-height: 178%;
        color: #666670;
        font-weight: 500;
        a{
            text-decoration: underline;
            color: #186FD4;
        }
    }
    &__nav{
        display: flex;
    }
    &__btn{
        width: 14.9375em;
        max-width: 100%;
        span{
            font-size: .9375em;
        }
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog{
            gap: 1.25em;
        }
        &__top{
            gap: .625em;
            .text33{
                font-size: 1.875em;
            }
        }
        &__desc{
            font-size: 1.0625em;
            line-height: 141%;
        }
    }

    .modal-form{
        gap: 1.25em;
    }

    .cookie-modal{
        padding: 1em 0;
        &__inner{
            padding: 0;
            display: block;
        }
        &__nav{
            margin: .75em 0 0;
        }
        &__btn{
            width: 100%;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        padding-top: .5em;
        &:after{
            display: none;
        }
        &__dialog{
            padding: 2em .94em 1.25em;
            margin: 0 auto;
        }
        &__close{
            position: absolute;
            top: 1em;
            right: 1em;
            width: 1em;
            path{
                stroke: $black;
            }
        }
    }
}
