@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 0.8125em;
        line-height: 139%;
    }
}

.m-btn-light-blue{
    border-radius: 0.25em;
    font-weight: 700;
    background: #F2F6FC;
    color: #2A5E8D;
    &:hover{
        color: #fff;
        background: $black5;
    }
}

.m-btn-light-blue2{
    height: 2.8125em;
    padding: 0 1.25em;
    border-radius: 0.375em;
    font-weight: 500;
    background: #F6F8FB;
    color: #117FE3;
    &:hover{
        color: #fff;
        background: $black5;
    }
    span{
        font-size: 0.875em;
        line-height: 120%;
    }
}

.m-btn-red-outline{
    height: 2.75em;
    padding: 0 1.25em;
    border-radius: 0.25em;
    font-weight: 700;
    color: #474B4E;
    border: 1px solid #EE0906;
    &:hover{
        color: #fff;
        background: #EE0906;
    }
    span{
        font-size: 0.75em;
        line-height: 120%;
    }
}

.m-btn-blue{
    height: 3.25em;
    padding: 0 1.81em;
    border-radius: 0.5em;
    font-weight: 700;
    text-transform: uppercase;
    background: $primary;
    border: 1px solid $primary;
    color: #fff;
    &:hover{
        color: #fff;
        background: $black5;
        border-color: $black5;
    }
}

.m-btn-black{
    height: 3.875em;
    padding: 0 1.69em;
    border-radius: 0.5em;
    font-weight: 700;
    text-transform: uppercase;
    background: $black5;
    color: #fff;
    &:hover{
        color: #fff;
        background: $red2;
    }
    img{
        width: .5em;
        margin-left: 1em;
        flex-shrink: 0;
    }
}
