.error404{
  &__inner{
    width: 45em;
    max-width: 100%;
    margin: 6.625em auto 0;
    @include grid100gap(3.44em);
  }
  &__img{
    display: block;
    width: 38.35706em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__desc{
    text-align: center;
    color: #535557;
    line-height: 161%;
  }
  &__nav{
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .47em));
    gap: .94em;
    width: 28.3125em;
    max-width: 100%;
  }
  &__btn{
    height: 4em;
    width: 100%;
  }
  .m-btn-light-blue2{
    span{
      font-size: .9375em;
    }
  }
}


@media screen and (max-width: 992px){
  .error404{
    &__inner{
      gap: 2.5em;
      margin-top: 3.75em;
    }
    &__img{
      width: 20.62506em;
    }
    &__nav{
      width: 18.125em;
      grid-template-columns: 100%;
    }
    &__desc{
      font-size: 1em;
      line-height: 150%;
    }
  }
}



@media screen and (max-width: 576px){
  .error404{
    &__inner{
      gap: 3.44em;
      margin-top: 2.5em;
    }
    &__img{
      width: 100%;
    }
    &__nav{
      width: 100%;
    }
  }
}
