.pass-recover{
  &__inner{
    max-width: 100%;
    width: 34em;
    padding: 2.5em;
    border-radius: 0.5em;
    border: 1px solid #EDF0F2;
    margin: 3.44em auto 0;
  }
  &__desc{
    text-align: center;
    color: #515151;
    .text16{
      line-height: 175%;
    }
  }
  &__img{
    width: 1.875em;
    display: block;
    margin: 0 auto .94em;
  }
}

.pass-form{
  @include grid100gap(.94em);
  margin-top: .94em;
}

.auth{
  &__inner{
    width: 86.625em;
    max-width: 100%;
    margin: 6.25em auto 0;
    border-radius: 0.5em;
    border: 1px solid #EDF0F2;
    padding: 2.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.login-form{
  @include grid100gap(.94em);
  width: 29em;
  .m-btn-light-blue2{
    span{
      font-size: .9375em;
    }
  }
  .m-check{
    display: inline-flex;
    margin: 0 auto;
  }
}

.auth-hint{
  max-width: 42.6875em;
  flex-grow: 1;
  padding-left: 3.125em;
  border-left: 1px solid #EAEAEA;
  display: flex;
  &__icon{
    flex-shrink: 0;
    margin-right: 1.81em;
    width: 2.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__top{
    display: flex;
    align-items: center;
    min-height: 2.8125em;
  }
  &__title{
    font-size: 1.75em;
    font-weight: 500;
    color: $black2;
  }
  &__text{
    color: #515151;
    margin-top: 2em;
    p{
      font-size: 1em;
      line-height: 175%;
    }
  }
  &__nav{
    margin-top: .625em;
    display: flex;
  }
  &__link{
    padding: 0 1.69em;
    height: 4.0625em;
  }
}

.reg{
  .auth__inner{
    width: 100%;
    margin-top: 1.25em;
    @media screen and (min-width: 993px){
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 2.19em));
      align-items: flex-start;
      gap: 4.38em;
    }
  }
  .login-form{
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    width: 100%;
    ._full{
      grid-column: 1/-1;
    }
    .m-check{
      width: 100%;
      label{
        padding-top: .16em;
        display: block;
        &:before{
          top: 50%;
          transform: translateY(-50%);
        }
        span{
          font-size: .8125em;
        }
      }
    }
  }
  .auth-hint{
    max-width: 100%;
  }
}

.error-output{
  grid-column: 1/-1;
  border-radius: 0.5em;
  border: 1px dashed #E52B0E;
  background: #FFF9F9;
  padding: 1.15em;
  @include grid100gap(1em);
  color: #E52B0E;
  .text15{
    line-height: 120%;
  }
}


@media screen and (max-width: 992px){
  .pass-recover{
    &__inner{
      margin-top: 2.5em;
      padding: 1.25em .94em;
      width: 26.5625em;
    }
  }

  .auth{
    &__inner{
      align-items: flex-start;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .9375em));
      gap: 1.875em;
      margin-top: 1.25em;
      padding: 1.25em .94em;
    }
  }

  .login-form{
    width: 100%;
  }

  .auth-hint{
    padding: 0;
    margin: 0;
    border: none;
    max-width: 100%;
    &__icon{
      width: 1.6875em;
      margin-right: .625em;
    }
    &__top{
      min-height: 1.9375em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__text{
      margin-top: .94em;
      p{
        font-size: .9375em;
        line-height: 167%;
      }
    }
    &__link{
      width: 100%;
    }
  }

  .reg{
    .login-form{
      grid-template-columns: 100%;
    }
  }

  .error-output{
    padding: .94em;
    gap: .44em;
    .text15{
      line-height: 146%;
    }
  }
}



@media screen and (max-width: 576px){
  .pass-recover{
    &__inner{
      margin-top: 1.25em;
    }
  }

  .auth{
    &__inner{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
  }
}
