.order{
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 35.625em) 35.375em;
    gap: 1.25em;
    margin-top: 1.875em;
  }
  &__content{
    border-radius: 0.5rem;
    border: 1px solid #EDF0F2;
  }
  &__bottom{
    padding: 1.56em 2.5em 2.5em;
  }
  &__subtitle{
    font-size: 1.25em;
    line-height: 140%;
  }
}

.order-list{
  padding: 2.19em 2.5em;
  border-radius: 0.375em;
  background: #FFF;
  box-shadow: 0px 4px 19px 0px rgba(23, 27, 32, 0.07);
  position: sticky;
  top: 1em;
  @include grid100gap(1.56em);
  &__title{
    font-size: 1.75em;
    line-height: 157%;
    font-weight: 500;
  }
  &__items,
  &__props{
    @include grid100gap(1.56em);
  }
  .prop-item{
    &._type2{
      .prop-item__title{
        font-size: 1.25em;
        line-height: 140%;
      }
      .prop-item__desc{
        font-size: 1.25em;
        line-height: 110%;
      }
    }
  }
  .m-check{
    label span{
      font-size: .8125em;
    }
  }
  &__btn{
    height: 4em;
  }
}

.ordered-product{
  display: flex;
  justify-content: space-between;
  .cart-item__info{
    max-width: 23.875em;
    margin-right: 1em;
    align-items: flex-start;
  }
  .cart-item__title{
    font-size: 1em;
    line-height: 162%;
  }
  &__prices{
    align-self: center;
  }
  .cart-item__price1{
    font-size: 1.125em;
  }
}

.order-login{
  padding: .625em 2.5em;
  border-bottom: 1px solid #EAEAEA;
  &__show{
    display: flex;
    align-items: center;
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    color: #383838;
  }
  &__btn{
    padding: 0 1.25em;
    margin-left: .625em;
    height: 2.6875em;
  }
  &__hidden{
    margin-top: 1.875em;
    max-width: 29em;
    display: none;
  }
}

.personal-data{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .78em));
  gap: 1.56em;
  margin-top: 1.56em;
  ._full{
    grid-column: 1/-1;
  }
}

.order-promo{
  margin-top: 1.56em;
  padding: 1.25em;
  border-radius: 0.375em;
  background: #F8F8F8;
  &__top{
    font-size: .9375em;
    line-height: 133%;
    color: $black2;
  }
  &__toggle{
    display: inline;
    color: #186FD4;
    text-decoration: underline;
    white-space: pre-wrap;
    &:hover{
      text-decoration: none;
    }
  }
  &__bottom{
    margin-top: .625em;
    display: none;
  }
  &__wrap{
    display: flex;
    .fg{
      width: 24.3125em;
    }
  }
  &__badge{
    @include center;
    height: 3.8125em;
    width: 16.25em;
    border-radius: 0.5em;
    background: #E7EAEE;
    margin-left: .625em;
    color: rgba(#5C6267,.4);
    font-weight: 500;
    span{
      font-size: .9375em;
    }
  }
}

.order-pay{
  margin-top: 1.56em;
  &__items{
    margin-top: .94em;
    @include grid100gap(.94em);
  }
  .m-radio{
    label{
      span{
        font-size: 1em;
        line-height: 137%;
      }
    }
  }
}

.order-address{
  margin-top: 1.56em;
  display: none;
  &._active{
    display: block;
  }
  &__fields{
    display: grid;
    grid-template-columns: calc(100% - 28.12em) 12.5em 12.5em;
    gap: 1.56em;
  }
}

.order-delivery{
  &__region{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .47em));
    gap: .94em;
    margin-top: 1.4375em;
    position: relative;
    z-index: 3;
  }
  &__ways{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.88em / 3));
    gap: .94em;
    margin-top: 1.4375em;
    &._disabled{
      .delivery-way{
        pointer-events: none;
        &__inner{
          background: #F6F6F6;
          opacity: .5;
        }
      }
    }
  }
}

.delivery-way{
  position: relative;
  &__inner{
    padding: 1.25em 1.25em 1.25em 3.63em;
    border-radius: 0.4375em;
    border: 1px solid #DFE2E4;
    min-height: 100%;
    position: relative;
    &:before{
      content: '';
      transition: all .100s;
      background-color: #fff;
      border: $border;
      border-radius: 50%;
      pointer-events: none;
    }
    &:after{
      content: '';
      transition: all .1s;
      opacity: 0;
      z-index: 2;
      pointer-events: none;
      border-radius: 50%;
      background-image: url(../img/radio3.svg);
      @include bgImgSize(100%);
    }
  }
  &__inner:after,
  &__inner:before{
    position: absolute;
    width: 1.4375em;
    height: 1.4375em;
    top: 1.25em;
    left: 1.25em;
    font-size: 1em;
  }
  &__radio{
    font-size: 1em;
  }
  &__radio{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    cursor: pointer;
    z-index: 2;
    &:checked~.delivery-way__inner{
      border-color: #186FD4;
      outline: 1px solid #186FD4;
      &:before{
        border-color: #186FD4;
      }
      &:after{
        opacity: 1;
      }
      .delivery-way__radios{
        display: block;
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__info{
    @include grid100gap(.41em);
  }
  &__title{
    font-size: 1em;
    line-height: 137.5%;
    color: $black2;
  }
  &__desc{
    font-size: .875em;
    line-height: 157%;
    color: #62676F;
  }
  &__radios{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    .m-radio{
      margin-top: .38em;
    }
  }
  &__address{
    margin-top: .625em;
    padding: .56em .69em;
    border-radius: 0.375em;
    background: #F6F8FB;
    display: flex;
    align-items: flex-start;
    color: #393C40;
    img{
      width: 1.375em;
      flex-shrink: 0;
      display: block;
      margin-right: .3125em;
    }
    span{
      font-size: 1em;
      line-height: 137.5%;
    }
  }
}


@media screen and (max-width: 992px){
  .order{
    &__inner{
      grid-template-columns: 100%;
      gap: 2.5em;
      margin-top: 1.25em;
    }
    &__bottom{
      padding: 1.56em 1.56em 2.5em;
    }
  }

  .order-login{
    padding: .625em 1.56em;
  }

  .order-list{
    padding: 1.25em 1.56em;
    gap: .94em;
    &__props,
    &__items{
      gap: .94em;
    }
    .prop-item{
      &._type2{
        .prop-item__title,
        .prop-item__desc{
          font-size: 1.0625em;
          line-height: 129%;
        }
      }
    }
  }

  .personal-data{
    margin-top: .94em;
    row-gap: .94em;
  }

  .order-promo{
    padding: .94em;
    margin-top: .94em;
    &__wrap{
      .fg{
        width: calc(100% - 16.25em - .625em);
      }
    }
  }

  .order-address,
  .order-pay{
    margin-top: .94em;
  }

  .order-address{
    &__fields{
      grid-template-columns: repeat(2,calc(50% - .47em));
      gap: .94em;
    }
    ._full-tablet{
      grid-column: 1/-1;
    }
  }

  .order-delivery{
    &__region{
      margin-top: 1.12em;
    }
    &__ways{
      margin-top: 1.12em;
      grid-template-columns: 100%;
    }
  }

  .delivery-way{
    &__address{
      @media screen and (min-width: 577px){
        align-items: center;
      }
      span{
        font-size: .875em;
        line-height: 143%;
      }
    }
    &__radios{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .order{
    &__bottom{
      padding: .94em .94em 2.5em;
    }
  }

  .order-login{
    padding: .625em .94em;
  }

  .order-list{
    padding: 1.25em;
    &__title{
      font-size: 1.5em;
    }
  }

  .ordered-product{
    @include grid100gap(.3125em);
    .cart-item__info{
      width: 100%;
    }
    .cart-item__title{
      -webkit-line-clamp: 3;
    }
    &__prices{
      padding-left: 6.565em;
    }
  }

  .personal-data{
    grid-template-columns: 100%;
  }

  .order-promo{
    &__wrap{
      display: block;
      .fg{
        width: 100%;
      }
    }
    &__badge{
      margin: .625em 0 0;
      width: 100%;
    }
  }

  .order-delivery{
    &__region{
      grid-template-columns: 100%;
    }
  }
}
