@import "../vars.scss";
.breadcrumbs {
    padding: 1.125em 0;
    margin-bottom:  1.19em;
    &__item {
        position: relative;
        display: inline;
        color: #2C333D;
        span{
            font-size: .9375em;
            line-height: 133%;
        }
        &:last-child {
            color: #91969D;
        }
        &:not(:first-child) {
            &:before {
                content: '/';
                margin: 0 .75em;
                font-size: .9375em;
                line-height: 133%;
            }
        }
    }
}



@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: .75em 0;
        margin-bottom: 1.25em;
        &__item{
            span{
                font-size: .8125em;
            }
            &:not(:first-child) {
                &:before {
                    font-size: .8125em;
                    line-height: 153%;
                    margin: 0 .625em;
                }
            }
        }
    }
}
