.catalog-item{
  &__inner{
    display: grid;
    grid-template-columns: 50em calc(100% - 74.4375em) 21.9375em;
    gap: 1.25em;
    margin-top: 2.5em;
  }
  &__col{
    &:nth-child(2){
      max-width: 25em;
    }
  }
}

.gallery{
  display: grid;
  grid-template-columns: 6.0625em calc(100% - 7.3125em);
  gap: 1.25em;
  height: 34em;
  &__img{
    display: block;
    @include imgCover(76.2%);
    img{
      transition: all .3s;
    }
  }
  &__small{
    margin-top: .94em;
    width: calc(100% + .94em);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      background: linear-gradient(179deg, rgba(255, 255, 255, 0.00) 0.87%, #FFF 50.01%);
      width: 100%;
      height: 4.625em;
      z-index: 2;
      pointer-events: none;
    }
    .swiper{
      &-slide{
        height: 5.875em;
        padding-bottom: 1.25em;
        position: relative;
        &:hover{
          cursor: pointer;
        }
        &:hover,
        &-thumb-active{
          .gallery__img:before{
            opacity: 1;
          }
        }
      }
      &-button{
        position: absolute;
        width: .9375em;
        z-index: 3;
        left: 50%;
        top: unset;
        bottom: 0;
        margin: 0;
        background-color: transparent;
        svg{
          width: 100%;
          height: auto;
        }
        &:hover{
          background-color: transparent;
          path{
            fill: #2F343A;
            opacity: 1;
          }
        }
      }
    }
    .gallery__img{
      padding: 0;
      height: 100%;
      border-radius: 0.375em;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: inherit;
        border: 2px solid $primary;
        z-index: 2;
        transition: all .3s;
        opacity: 0;
      }
    }
  }
  &__big{
    width: 100%;
    position: relative;
    .gallery__img{
      padding: 0;
      height: 34em;
    }
  }
}

.zoom{
  position: absolute;
  left: 1.25em;
  bottom: 1.25em;
  width: 1em;
  pointer-events: none;
  z-index: 2;
  @include center;
  img{
    width: 100%;
    height: auto;
  }
}

.catalog-cart{
  position: sticky;
  top: 1em;
  border-radius: 0.375rem;
  background: #FFF;
  box-shadow: 0 .25em 1.1875em 0 rgba(23, 27, 32, 0.07);
  padding: 1.56em;
  @include grid100gap(.94em);
  &__articul{
    color: #787C80;
  }
  &__has{
    display: flex;
    align-items: center;
    color: #3B4048;
    img{
      width: .8125em;
      margin-right: .625em;
      flex-shrink: 0;
    }
  }
  &__prices{
    display: flex;
    .m-badge3{
      margin: -.625em 0 0 .44em;
      align-self: flex-start;
    }
  }
  &__price{
    font-size: 1.875em;
    font-weight: 500;
    color: #003B73;
  }
  .product-card__price{
    margin: -.5em 0 0 .5em;
    align-self: flex-start;
  }
  &__count{
    display: flex;
    align-items: center;
    .cartcalc{
      margin-left: 1.56em;
    }
  }
  .cart-btn{
    height: 3.875em;
    text-transform: uppercase;
    font-weight: 700;
    span{
      font-size: 0.8125em;
    }
  }
  &__buy{
    height: 3em;
    border-radius: .5em;
    span{
      font-size: .9375em;
    }
  }
  &__action{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 .75em;
    .favourite-btn,
    .compare-btn{
      white-space: nowrap;
    }
  }
}

.cartcalc{
  width: 11.8125em;
  display: grid;
  grid-template-columns: 3.8125em calc(100% - 7.625em) 3.8125em;
  input{
    width: 100%;
    text-align: center;
    height: 100%;
    font-size: 1.0625em;
    color: #2C333D;
    border: none;
  }
  button{
    height: 2.6875em;
    @include center;
    border-radius: 56.25em;
    background: #F3F4F5;
    svg{
      max-width: .6875em;
    }
    &:hover{
      background: $black5;
      path{
        fill: #fff;
      }
    }
  }
}

.props{
  &__items{
    @include grid100gap(.69em);
  }
  &__nav{
    display: flex;
    margin-top: .69em;
  }
}

.prop-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:after{
    content: '';
    flex-grow: 1;
    height: 1px;
    min-width: .375em;
    border-top: 1px dashed #D1D1D1;
    margin: 0 .94em;
  }
  &__title{
    color: #2C333D;
  }
  &__desc{
    color: #7D7F83;
    order: 2;
  }
  &__title,
  &__desc{
    font-size: .9375em;
    line-height: 187%;
  }
  &._type2{
    .prop-item__title{
      font-weight: 500;
    }
    .prop-item__desc{
      font-size: 1.375em;
      color: #003B73;
      line-height: 100%;
    }
  }
}

.catalog-advantages{
  margin-top: 1.625em;
  @include grid100gap(2.31em);
  padding: 1.25em;
  border-radius: 0.375em;
  border: 1px solid #EDF0F2;
  .advantage-item{
    margin: 0 !important;
    &:after{
      display: none;
    }
  }
  .black-text3{
    color: #515257;
  }
}

.catalog-item2{
  &__content{
    margin-top: 1.875em;
  }
  .props{
    margin: 1.25em 0 2.5em;
    max-width: 95em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 3em));
    gap: 1em 6em;
  }
  &__hint{
    line-height: 180%;
  }
}

.tab2{
  margin-right: 1.56em;
  flex-shrink: 0;
  padding: 0 .94em;
  color: #96999E;
  height: 3.9375em;
  display: flex;
  align-items: center;
  border-bottom: 1px solid transparent;
  transition: all .3s;
  font-weight: 500;
  span{
    font-size: 1.1875em;
    line-height: 120%;
  }
  &:hover{
    color: $black2;
    cursor: pointer;
  }
  &._active{
    color: $black2;
    pointer-events: none;
    border-color: #EE0906;
  }
}

.about-product{
  max-width: 93.9375em;
  &__desc{
    margin-top: 1.25em;
  }
  &__desc2{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: calc(100% - 31.125em) 29.25em;
    gap: 1.875em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
}

.pay-product,
.delivery-product{
  .text{
    margin-top: 1.44em;
    ul{
      gap: .56em;
    }
    p{
      font-size: 1em;
    }
    li{
      font-size: .9375em;
    }
  }
}

.pay-product{
  margin-top: 1.44em;
}


@media screen and (max-width: 992px){
  .catalog-item{
    overflow: hidden;
    &__inner{
      grid-template-columns: 20.625em calc(100% - 22.875em);
      gap: 1.875em 2.25em;
      margin-top: 2.125em;
    }
    &__col{
      &:nth-child(2){
        max-width: 100%;
        order: 2;
        grid-column: 1/-1;
        display: grid;
        grid-template-columns: calc(100% - 23.69em) 22em;
        gap: 1.69em;
        align-items: center;
      }
      .props{
        .prop-item{
          &:nth-child(4)~.prop-item{
            display: none;
          }
        }
      }
    }
  }

  .gallery{
    grid-template-columns: 100%;
    height: unset;
    &__big{
      order: -1;
      .gallery__img{
        height: unset;
        padding-bottom: 79.7%;
      }
    }
    &__small{
      width: calc(100% + 1.25em);
      .swiper{
        &-slide{
          width: calc(100% / 3);
          height: unset;
          padding: 0 1.25em 0 0;
        }
        &-button{
          left: unset;
          right: 0;
          top: 50%;
          transform: translate(-100%,-50%) rotate(-90deg);
        }
      }
      .gallery__img{
        height: unset;
        padding-bottom: 76.2%;
      }
      &:before{
        width: 6.0625em;
        height: 100%;
        left: unset;
        right: 0;
        background: linear-gradient(90deg, rgba(255, 255, 255, 0.00) 41.75%, #FFF 86.08%);
      }
    }
  }

  .zoom{
    bottom: .625em;
    left: .625em;
  }

  .catalog-advantages{
    gap: 1.75em;
    order: -1;
    margin: 0;
  }

  .catalog-item2{
    .props{
      margin-bottom: 1.25em;
      grid-template-columns: 100%;
    }
  }

  .tab2{
    margin-right: 1.25em;
  }

  .about-product{
    &__desc{
      margin-top: .94em;
    }
    &__desc2{
      margin-top: .94em;
      grid-template-columns: calc(100% - 16.065em) 15.125em;
      gap: .94em;
    }
    &__img{
      img{
        position: sticky;
        top: 1em;
      }
    }
  }

  .pay-product,
  .delivery-product{
    .text{
      margin-top: 1.25em;
    }
  }

  .pay-product{
    margin-top: 1.25em;
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    overflow: hidden;
    &__inner{
      grid-template-columns: 100%;
      margin-top: 1.94em;
      gap: 2.19em;
    }
    &__col {
      &:nth-child(2) {
        grid-template-columns: 100%;
      }
    }
    .props{
      display: none;
    }
  }

  .catalog-item2{
    &__content{
      margin-top: 1.56em;
    }
    .props{
      margin: 1.875em 0;
    }
  }

  .about-product{
    &__desc2{
      grid-template-columns: 100%;
    }
  }

  .pay-product,
  .delivery-product{
    .text{
      margin-top: .94em;
    }
  }

  .pay-product{
    margin-top: .94em;
  }
}
