.collection{
  .tabs{
    margin-top: 1.875em;
  }
  &__content{
    margin-top: 3.125em;
  }
}

.tabs{
  display: flex;
}

.tab{
  flex-shrink: 0;
  @include center;
  padding: 0 1.25em;
  border-radius: 0.375em;
  border: 1px solid #F2F4F5;
  height: 2.8125em;
  margin-right: .625em;
  color: #5B5B5C;
  font-weight: 500;
  transition: all .3s;
  cursor: pointer;
  span{
    font-size: 0.875em;
    line-height: 120%;
  }
  &._active,
  &:hover{
    background: #F6F8FB;
    border-color: #F6F8FB;
    color: #117FE3;
  }
  &._active{
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.product-slider{
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: 20%;
      height: auto;
      padding-right: 1.25em;
    }
    &-button{
      pointer-events: unset !important;
      &-prev{
        left: -.31em;
      }
      &-next{
        right: -.31em;
      }
    }
  }
}

.product-card{
  height: 100%;
  padding: 0 .94em;
  display: flex;
  flex-direction: column;
  &__top{
    padding: .94em;
    position: relative;
  }
  &__img{
    display: block;
    height: 12.625em;
    position: relative;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__badges{
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
  }
  &__articul{
    margin-top: .625em;
    color: #A8ADB2;
  }
  &__title{
    margin-top: .625em;
    display: flex;
    align-items: center;
    height: 4.9375em;
    .text18{
      @include truncate(3);
    }
  }
  &__nums{
    display: flex;
    align-items: center;
    margin-top: .625em;
    .m-badge3{
      margin-left: .44em;
    }
  }
  &__price{
    color: #8F9296;
    text-decoration-line: line-through;
    margin-right: .625em;
    .text15{
      line-height: 146%;
    }
  }
  &__price2{
    color: $blue;
    font-weight: 500;
  }
  &__nav{
    padding-top: .625em;
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .cart-btn{
    min-width: 7.5em;
  }
  &__buttons{
    display: flex;
  }
  .compare-btn,
  .favourite-btn{
    margin-left: .56em;
  }
  &__bottom{
    margin-top: auto;
  }
}

.m-badge2,
.m-badge3{
  display: inline-flex;
  align-items: center;
}

.m-badge2{
  border-radius: 0.3125em;
  border: 1px solid #F90;
  padding: 0 .5em;
  height: 1.9375em;
  color: #F90;
  font-weight: 500;
  span{
    font-size: 0.8125em;
    line-height: 147%;
  }
  &._red{
    color: #EE063E;
    border-color: #EE063E;
  }
  &._green{
    color: #1B9665;
    border-color: #1B9665;
  }
}

.m-badge3{
  height: 1.5em;
  padding: 0 .3125em;
  border-radius: 0.25em;
  background: #1B9665;
  color: #fff;
  font-weight: 700;
  span{
    font-size: .6875em;
    line-height: 120%;
  }
  &._red{
    background: #EE063E;
  }
  &._yellow{
    background: #F90;
  }
}

.cart-btn{
  padding: 0;
  height: 2.5em;
  text-transform: unset;
  border-radius: 0.375em;
  span{
    font-size: .9375em;
  }
  img{
    margin-right: .69em;
    flex-shrink: 0;
    width: 1.5em;
  }
  ._view2{
    display: none;
  }
  &._added{
    &:not(:hover){
      background: transparent;
      color: $primary;
    }
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
}

.compare-btn,
.favourite-btn{
  display: flex;
  align-items: center;
  &__icon{
    flex-shrink: 0;
    width: 2.5em;
    height: 2.5em;
    border-radius: 0.375em;
    border: 1px solid #F3F4F5;
    position: relative;
    transition: all .3s;
    @include center;
    &:not(:last-child){
      margin-right: .5em;
    }
  }
  &:hover{
    .compare-btn,
    .favourite-btn{
      &__icon{
        border-color: $primary;
      }
    }
  }
  img{
    @include centerAbsolute;
    width: 2.0625em;
  }
  ._view2{
    opacity: 0;
  }
  &._added{
    ._view1{
      opacity: 0;
    }
    ._view2{
      opacity: 1;
    }
  }
}

.hits{
  @media screen and (min-width: 993px){
    .product-slider{
      grid-column: 1/-2;
      .swiper{
        &-slide{
          width: 25%;
        }
      }
    }
  }
}

.sale-product{
  padding: 1.56em 1.56em 4.06em;
  border-radius: 0.5em;
  border: 2px solid $primary;
  position: relative;
  &__top{
    display: flex;
    justify-content: space-between;
    .text20{
      max-width: 8em;
      line-height: 110%;
    }
    .m-badge3{
      margin-top: .21em;
    }
  }
  &__img{
    display: block;
    margin-top: 1.75em;
    height: 10.375em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  .product-card__title{
    text-align: center;
    margin: 0;
  }
  .product-card__nums{
    justify-content: center;
    margin: -.6em 0 0 0;
    .product-card__price2{
      color: #1B9665;
    }
  }
  .cart-btn{
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 100%;
    height: 3em;
    margin: 0;
    border-radius: 0 0 0.5em 0.5em;
    border-left: none;
    border-right: none;
  }
}


@media screen and (max-width: 992px){
  .tabs{
    overflow-x: auto;
    flex-wrap: nowrap;
    @include fullScreenTablet;
    @include nullScroll;
  }

  .tab{
    height: 2.4375em;
    padding: 0 .94em;
    span{
      font-size: .8125em;
    }
  }

  .collection{
    .tabs{
      margin-top: .94em;
    }
    &__content{
      margin-top: 1.56em;
    }
  }

  .hits{
    grid-template-columns: 100%;
    .sale-product{
      display: none;
    }
  }

  .product-slider{
    .swiper{
      &-container{
        width: calc(100% + 1.56em);
      }
      &-slide{
        width: calc(100% / 3);
        padding-right: 1.56em;
      }
      &-button{
        &-prev{
          left: -.625em;
        }
        &-next{
          right: -.625em;
        }
      }
    }
  }

  .product-card{
    padding: 0 .19em;
    &__title{
      .text18{
        font-size: 1em;
        line-height: 144%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .tabs{
    @include fullScreenMob;
  }

  .collection{
    overflow: hidden;
  }

  .product-slider{
    .swiper{
      &-container{
        width: 100%;
        overflow: unset;
      }
      &-slide{
        width: 11.4em;
        padding-right: 1.56em;
      }
      &-button{
        position: static;
        margin: 0 .47em;
        transform: unset;
        width: 2.5em;
        height: 2.5em;
        svg{
          width: .5em;
        }
      }
      &-nav{
        margin-top: 1.56em;
        @include center;
      }
    }
  }

  .product-card{
    padding: 0;
  }

  .hits{
    gap: 1.875em;
    .sale-product{
      padding-left: 2.06em;
      padding-right: 2.06em;
      display: block;
    }
  }

  .sale-product{
    .product-card__title{
      max-width: 16em;
      margin-left: auto;
      margin-right: auto;
      .text18{
        font-size: 1.1875em;
      }
    }
  }
}
