.favourite{
  .catalog__items{
    margin: 0;
  }
}

.favourite-nav{
  padding: .625em 1.5625em 1.5625em;
  border-radius: 0.375rem;
  box-shadow: 0px 4px 19px 0px rgba(23, 27, 32, 0.07);
  position: sticky;
  top: 1em;
  .blog-nav__links{
    margin: 0;
  }
  &__select{
    display: none;
  }
  &__buttons{
    margin-top: .94em;
    @include grid100gap(.94em);
    .m-btn{
      padding: 0;
    }
    .m-btn-blue{
      height: 4.0625em;
    }
    .m-btn-light-blue2{
      height: 3em;
      span{
        font-size: .9375em;
      }
    }
  }
}

.favourite-empty{
  &__inner{
    width: 33.875em;
    max-width: 100%;
    margin: 5.625em auto 0;
    @include grid100gap(1.25em);
    text-align: center;
  }
  &__img{
    width: 6.25em;
    margin: 0 auto;
    display: block;
  }
  &__desc{
    color: #383838;
    line-height: 147%;
    a{
      font-weight: 700;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
    .m-btn{
      padding: 0 2em;
      height: 4.1875em;
    }
  }
}


@media screen and (max-width: 992px){
  .favourite{
    .catalog__inner{
      margin-top: 1.25em;
    }
  }

  .favourite-nav{
    padding: 1.25em .94em;
    margin-bottom: 2.5em;
    position: relative;
    z-index: 3;
    &__select{
      display: block;
    }
    .blog-nav__links{
      display: none;
    }
    @media screen and (min-width: 577px){
      display: grid;
      grid-template-columns: 15.125em calc(100% - 16.065em);
      gap: .94em;
      padding: .94em;
      &__buttons{
        margin: 0;
        grid-template-columns: calc(100% - 11.7525em) 10.8125em;
      }
      .m-btn{
        height: 3.8125em;
      }
    }
  }

  .favourite-empty{
    &__inner{
      margin-top: 3.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .favourite-empty{
    &__inner{
      margin-top: 2.5em;
    }
  }
}
