.text{
  color: #2C333D;
  p,li{
    font-size: .9375em;
    line-height: 173%;
  }
  p,ul{
    @include lastM(0 0 .9em);
  }
  ul{
    @include grid100gap(1em);
    li{
      position: relative;
      padding-left: .93em;
      &:before{
        content: '';
        position: absolute;
        top: .7em;
        left: 0;
        width: .33em;
        height: .33em;
        border-radius: 50%;
        background-color: #EE0906;
      }
    }
  }
  h2{
    font-weight: 700;
    margin: .82em 0 .6em;
  }
  h3{
    font-weight: 500;
    margin-bottom: 1em;
  }
  h4{
    font-weight: 500;
    margin-bottom: 1.11em;
  }
  a{
    color: #186FD4;
    text-decoration: underline;
  }
  mark{
    font-weight: 700;
    color: #186FD4;
    background-color: transparent;
  }
  hr{
    height: 2em;
    display: block;
    opacity: 0;
  }
  &._type2{
    p,li{
      font-size: 1em;
      line-height: 168%;
    }
    ul{
      gap: .25em;


    }
  }
}


.text._type2,
.conf .text{
  ul{
    padding-left: .5em;
    li{
      &:before{
        top: .65625em;
        width: .375em;
        height: .375em;
        background-color: $black2;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .text{
    ul{
      gap: .625em;
    }
    p,li{
      font-size: .875em;
    }
    h2{
      margin: .87em 0 .65em;
    }
  }
}


@media screen and (max-width: 576px){
  .text{
    ul{
      gap: 1em;
    }
    ul,
    p{
      @include lastM(0 0 1em);
    }
  }
}
