.blog{
  .text48{
    max-width: 27em;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 21.9375em) 20.6875em;
    gap: 3.125em 1.25em;
    margin-top: 1.875em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
    gap: 1.875em;
  }
  .subscribe{
    margin-top: 1.25em;
  }
  &__select{
    display: none;
  }
}

.blog-nav{
  padding: 1.25em 1.56em;
  border-radius: 0.5em;
  border: 1px solid #ECECEC;
  &__title{
    font-size: 1.4375em;
    line-height: 117%;
    font-weight: 500;
  }
  &__links{
    margin-top: .625em;
  }
  &__link{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 3.4375em;
    font-weight: 500;
    &._active{
      .blog-nav__link-title{
        font-weight: 700;
      }
    }
    &._active,
    &:hover{
      .blog-nav__link-title,
      .blog-nav__link-num{
        color: #186FD4;
      }
    }
    &:not(:last-child){
      border-bottom: 1px solid #F4F4F4;
    }
    &-title{
      font-size: .8125em;
      line-height: 116%;
      text-transform: uppercase;
      color: #373B3F;
    }
    &-num{
      @include center;
      border-radius: 56.25em;
      border: 1px solid #F3F3F3;
      height: 1.5625em;
      min-width: 1.5625em;
      padding: 0 .5em;
      color: #686868;
      span{
        font-size: 0.6875em;
        line-height: 137%;
      }
    }
  }
}

.subscribe{
  @include grid100gap(.81em);
  padding: 1.875em 1.56em;
  border-radius: 0.5em;
  background: #F9FAFB;
  text-align: center;
  &__top,
  &__wrap{
    @include grid100gap(.81em);
  }
  &__title{
    color: #1D1D27;
    line-height: 144%;
  }
  &__desc{
    color: #6A6F75;
    line-height: 135%;
  }
  .consult-form__btn{
    width: 100%;
    margin: 0;
  }
  .m-check{
    text-align: left;
    margin-top: 1em;
    label{
      color: #696D70;
      span{
        font-size: .8125em;
        line-height: 160%;
      }
    }
  }
}

.blog-item{
  .blog__grid{
    margin: 0;
  }
}

.article{
  margin-top: 1.69em;
  &__top{
    display: flex;
    align-items: center;
  }
  &__badge{
    padding: 0 .625em;
    border-radius: 0.3125em;
    background: #F7F9FB;
    height: 2.625em;
    display: flex;
    align-items: center;
    font-weight: 500;
    text-transform: uppercase;
    span{
      font-size: .8125em;
    }
  }
  &__date{
    display: flex;
    align-items: center;
    margin-left: .75em;
    color: #4F4F4F;
    &:before{
      content: '';
      flex-shrink: 0;
      width: 0.375em;
      height: 0.375em;
      margin-right: .75em;
      border-radius: 50%;
      background-color: #DEDEDE;
    }
  }
  .text{
    color: #1E1E1E;
    margin-top: 1.69em;
    p{
      &:nth-child(1){
        font-size: 1.125em;
        line-height: 178%;
      }
    }
    img{
      display: block;
      width: 100%;
      margin: 1.69em 0;
    }
  }
}

.delivery{
  .text{
    color: #1E1E1E;
    p{
      font-size: 1em;
      line-height: 168%;
      @include lastM(0 0 1em);
    }
    ul{
      padding-left: .94em;
      gap: .56em;
    }
  }
}

.hint{
  margin: 1.25em 0;
  padding: .94em 1.25em;
  background: #F9FAFB;
  color: #003B73;
  font-weight: 500;
  .text16{
    line-height: 169%;
  }
}

.delivery-slider{
  margin-top: 3.75em;
}

.img-card{
  &__wrap{
    display: block;
    border-radius: .5em;
    @include imgCover(59.7%);
  }
}

.qa{
  margin-top: 1.25em;
  &__items{
    margin-top: 1.25em;
  }
}

.qa-card{
  &._active{
    .qa-card__btn{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
  &__show{
    border-bottom: 1px solid #EDF0F2;
    padding: .625em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__btn{
    width: 2.75em;
    height: 2.75em;
    background-color: #EBF7FF;
    border-radius: 50%;
    margin-left: 1em;
    flex-shrink: 0;
    position: relative;
    svg{
      @include centerAbsolute;
      width: 1em;
    }
    ._view2{
      opacity: 0;
    }
  }
  &__hidden{
    display: none;
    padding: 1.25em;
  }
  .text{
    color: #64676A;
    p{
      font-size: 1em;
      line-height: 169%;
    }
  }
}

.reviews{
  &__links{
    display: flex;
  }
  &__link{
    padding: 0 1.25em;
    margin-right: .625em;
    height: 2.8125em;
    border-radius: 0.375em;
    border: 1px solid #F2F4F5;
    color: #5B5B5C;
    font-weight: 500;
    span{
      font-size: .875em;
      line-height: 120%;
    }
    &:hover,
    &._active{
      background-color: #F6F8FB;
      border-color: #F6F8FB;
      color: #117FE3;
    }
  }
  &__items{
    margin-top: 2.5em;
    @include grid100gap(1.875em);
  }
}

.review-card{
  padding: 1.56em;
  border-radius: 0.5em;
  border: 1px solid #EDF0F2;
  &__top{
    margin-bottom: .625em;
    .text19{
      line-height: 136%;
    }
    .text15{
      color: #60656C;
      line-height: 173%;
    }
  }
  .text16{
    line-height: 162%;
  }
  &__images{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + .625em);
  }
  &__img{
    width: 12.3125em;
    height: 6.9375em;
    margin: .625em .625em 0 0;
    display: block;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
}

.new-review{
  margin-top: 6.62em;

  .consult-form{
    margin: 2.25em 0 0;
  }
}


@media screen and (max-width: 992px){
  .blog{
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
    }
    .subscribe{
      margin: 0;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .5025em));
      gap: 1.125em 1.05em;
    }
    &__select{
      margin-bottom: 1.25em;
      display: block;
    }
    .catalog-promo{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      margin-bottom: 1.25em;
    }
  }

  .blog-nav{
    display: none;
  }

  .subscribe{
    @media screen and (min-width: 577px){
      &__wrap{
        display: flex;
      }
      .consult-form__btn{
        width: 14.9375em;
        flex-shrink: 0;
        margin: 0 0 0 .81em;
      }
      .fg{
        flex-grow: 1;
      }
      .m-check{
        display: inline-block;
        margin: 0 auto;
      }
    }
  }

  .article{
    margin-top: 1.25em;
    .text{
      margin-top: 1.25em;
      img{
        margin: 1.25em 0;
      }
      p{
        &:nth-child(1){
          font-size: 1em;
          line-height: 169%;
        }
      }
      p,li{
        font-size: .9375em;
        line-height: 173%;
      }
    }
  }

  .delivery-slider{
    display: none;
  }

  .qa-card{
    &__hidden{
      padding: .625em 1.25em;
    }
  }

  .reviews{
    &__items{
      margin-top: 1.25em;
    }
  }

  .review-card{
    padding: 1.25em;
    &__img{
      height: 4.6875em;
      width: 8.3125em;
    }
  }
}


@media screen and (max-width: 576px){
  .blog{
    &__items{
      grid-template-columns: 100%;
      gap: 1.875em;
    }
    .catalog-promo{
      grid-template-columns: 100%;
      gap: 1.875em;
      margin-bottom: 1.875em;
    }
  }

  .subscribe{
    padding: 1.25em .94em;
  }

  .review-card{
    &__images{
      margin-top: .625em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
    &__img{
      width: 100%;
      margin: 0;
    }
  }
}
