.about-page{
  margin: 0;
  padding: 0 0 3.25em;
  background: #F9FAFB;
  .text{
    margin-top: .625em;
  }
  .about__desc{
    margin-top: .625em;
    max-width: 45em;
  }
  .about__content{
    max-width: 56.1875em;
  }
  @media screen and (max-width: 1825px){
    .about__content{
      max-width: 48em;
    }
  }
}

.history{
  &__grid{
    margin-top: 4.12em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
}

.history-card{
  padding: 2.19em 1.56em;
  border-radius: 0.5em;
  border: 3px solid #E6EEF6;
  position: relative;
  color: $black2;
  &__year{
    position: absolute;
    padding: 0 .94em;
    left: .81em;
    top: 0;
    transform: translateY(-50%);
    background-color: #fff;
    font-weight: 700;
    color: #186FD4;
    span{
      font-size: 1.6875em;
    }
  }
  .text{
    margin-top: .94em;
    ul{
      gap: .94em;
      b{
        white-space: nowrap;
      }
    }
  }
}

.today-card{
  border-color: #186FD4;
  padding: 1.56em 7em;
  background-image: url(../img/today-bg.svg);
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: right bottom;
  &__title{
    color: #186FD4;
    font-size: 1.75em;
    text-align: center;
    font-weight: 500;
  }
  &__grid{
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    gap: 1.56em;
    align-items: flex-start;
    margin-top: 1.18em;
  }
  &__group{
    @include grid100gap(.625em);
  }
}

.why{
  padding-top: 5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 18em / 4));
    gap: 6em;
    margin-top: 3em;
  }
}

.why-card{
  display: flex;
  align-items: flex-start;
  &__icon{
    width: 3.25em;
    flex-shrink: 0;
    margin-right: .94em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding-top: .94em;
    @include grid100gap(.69em);
  }
  .text20{
    color: #3B3C3F;
    line-height: 120%;
  }
  .text15{
    color: #6B6C72;
    line-height: 160%;
  }
}


@media screen and (max-width: 992px){
  .about-page{
    .about__desc{
      margin-top: 1.19em;
    }
    .text{
      p{
        &:nth-last-child(1),
        &:nth-last-child(2){
          max-width: 25em;
        }
      }
    }
  }

  .history{
    &__grid{
      margin-top: 1.625em;
      grid-template-columns: 100%;
      gap: 1.56em;
    }
  }

  .today-card{
    padding: 1.38em 4.81em;
    &__title{
      font-size: 1.4375em;
    }
    &__grid{
      gap: .94em;
    }
  }

  .why{
    padding-top: 2.31em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 1.69em));
      gap: .94em 3.38em;
      margin-top: .94em;
    }
  }

  .why-card{
    &__content{
      padding-top: .56em;
      gap: .25em;
    }
    .text20{
      font-size: 1.0625em;
      line-height: 136%;
    }
  }
}


@media screen and (max-width: 576px){
  .about-page{
    padding-bottom: 0;
    .about__img{
      position: static;
      width: 100%;
      height: auto;
      display: block;
      @include fullScreenMob;
      padding: 0;
      img{
        width: 100%;
        height: auto;
      }
    }
  }

  .history{
    .text33{
      text-align: center;
    }
    &__grid{
      margin-top: 2.5em;
      gap: 1.88em;
    }
  }

  .history-card{
    &__year{
      left: 50%;
      transform: translate(-50%,-50%);
    }
  }

  .today-card{
    padding: 1.56em 1.38em;
    background-size: auto 18.75em;
    background-image: url(../img/today-bg2.svg);
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .why{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
