.conf{
  .text{
    margin-top: 1.875em;
    ul{
      gap: .25em;
    }
    a{
      color: #E52B0E;
      text-decoration: underline;
      font-weight: 700;
    }
  }
  &__hint{
    padding: 1.25em 1.56em;
    border-radius: 0.5em;
    background: #F8F8F8;
    margin-top: 1.875em;
    .text{
      margin: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .conf{
    &__hint{
      padding: .94em;
      margin-top: .44em;
    }
  }
}


@media screen and (max-width: 576px){
  .conf{
    &__hint{
      margin-top: 1.25em;
    }
  }
}
