.brands-slider{
  margin-top: 2.5em;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1.25em - 1px);
      overflow: hidden;
    }
    &-slide{
      width: calc(100% / 7);
      padding-right: 1.25em;
      transition: all .3s;
      @media screen and (min-width: 993px){
        &-next+.swiper-slide+.swiper-slide{
          &+.swiper-slide+.swiper-slide+.swiper-slide+.swiper-slide{
            opacity: 0;
          }
        }
      }
    }
    &-button{
      @media screen and (min-width: 993px){
        width: 2.5em;
        height: 2.5em;
        svg{
          width: .5em;
        }
      }
      &-prev{
        left: -1.25em;
      }
      &-next{
        right: -1.25em;
      }
    }
  }
}

.brand-card{
  display: block;
  border-radius: 0.5em;
  border: 1px solid #F2F4F5;
  background: #FFF;
  height: 5em;
  position: relative;
  img{
    @include centerAbsolute;
    max-width: calc(100% - 2.75em);
    max-height: calc(100% - 1.75em);
    display: block;
  }
  &:hover{
    border-color: $primary;
  }
}

.about{
  padding: 4.19em 0 5em;
  position: relative;
  &__img{
    position: absolute;
    bottom: 0;
    right: 0;
    height: 100%;
    img{
      height: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__content{
    max-width: 51.8125em;
    position: relative;
    z-index: 2;
  }
  &__desc{
    margin-top: 1.06em;
  }
  &__text{
    margin-top: 2.06em;
  }
  &__items{
    margin-top: 2.06em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .25em));
    justify-content: space-between;
  }
  &__nav{
    margin-top: 1.44em;
    display: flex;
  }
}

.about-item{
  .text26{
    color: #117FE3;
    line-height: 85%;
  }
  .text15{
    margin-top: .55em;
    color: #44474F;
  }
}

.news{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.news-slider{
  position: relative;
  margin-top: 1.875em;
  .swiper{
    &-container{
      overflow: hidden;
      width: calc(100% + 1.25em);
    }
    &-slide{
      width: 25%;
      padding-right: 1.25em;
    }
    &-button{
      width: 2.5em;
      height: 2.5em;
      svg{
        width: .5em;
      }
      &-prev{
        left: -1.25em;
      }
      &-next{
        right: -1.25em;
      }
    }
  }
}

.news-card{
  &__img{
    display: block;
    border-radius: .5em;
    @include imgCover(53.6%);
  }
  &__content{
    margin-top: 1.06em;
    @include grid100gap(.75em);
  }
  .text16{
    color: #393939;
  }
}


@media screen and (max-width: 992px){
  .brands-slider{
    margin-top: 1.56em;
    .swiper{
      &-container{
        width: calc(100% + .94em - 1px);
      }
      &-button{
        &-prev{
          left: -.625em;
        }
        &-next{
          right: -.625em;
        }
      }
      &-slide{
        width: 25%;
        padding-right: .94em;
      }
    }
  }

  .brand-card{
    height: 4.25em;
  }

  .about{
    padding: 1.875em 0;
    margin: 1.5625em 0;
    &__desc{
      margin-top: .94em;
      .text17{
        font-size: 1em;
        line-height: 144%;
      }
    }
    &__text{
      max-width: 23.6875em;
      .text15{
        line-height: 173%;
      }
    }
    &__img{
      height: 18.875em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__items{
      margin-top: 1.94em;
    }
    &__nav{
      margin-top: .94em;
    }
    &__btn{
      height: 3.3125em;
      padding: 0 1.31em;
    }
  }

  .about-item{
    .text15{
      font-size: .875em;
      line-height: 129%;
    }
  }

  .news{
    overflow: hidden;
  }

  .news-slider{
    margin-top: 1.56em;
    .swiper{
      &-container{
        width: 100%;
        overflow: unset;
      }
      &-slide{
        width: 18.44em;
        padding-right: .94em;
      }
      &-button{
        position: static;
        margin: 0 .47em;
        transform: unset;
      }
      &-nav{
        margin-top: 1.56em;
        @include center;
      }
    }
  }

  .news-card{
    &__content{
      margin-top: .625em;
      gap: .5em;
    }
  }
}


@media screen and (max-width: 576px){
  .brands{
    overflow: hidden;
  }

  .brands-slider{
    .swiper{
      &-container{
        width: 100%;
        overflow: unset;
      }
      &-slide{
        width: 11.44em;
      }
      &-nav{
        display: none;
      }
    }
  }

  .about{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .4375em));
      gap: 2em .875em;
    }
    &__img{
      display: none;
    }
  }

  .news{
    &__inner{
      position: relative;
      padding-bottom: 4.0625em;
    }
    &__link{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      white-space: nowrap;
    }
  }

  .news2{
    .news__inner{
      padding-bottom: 0;
    }
  }
}
