.fg {
  position: relative;
  margin: 0;
  z-index: 2;
  textarea,
  input ,
  .nice-select{
    border: $border;
    padding: 0 1.25em;
    height: 3.8125em;
    width: 100%;
    font-size: 1em;
    border-radius: .4375em;
    background-color: #fff;
    color: #2C333D;
    &::placeholder {
      color: #ADB1B7;
    }
    &:focus {
      border-color: $primary;
    }
  }
  textarea{
    height: 5.5625em;
    resize: vertical;
    padding-top: 1.5em;
    padding-bottom: 1.5em;
  }
  label{
    transition: all .2s;
    font-size: .875em;
    line-height: 157%;
    display: block;
    margin-bottom: .71em;
    span{
      color: #E52B0E;
    }
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.4375em;
    white-space: pre-wrap;
    &:focus{
      border: $border;
    }
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: .5em;
      height: .75em;
      background-image: url(../img/arrow-bottom.svg);
      @include bgImgSize(100%);
    }
    .current{
      font-size: 1em;
      line-height: 157%;
    }
    .list{
      margin-top: -.25em;
      border-radius: 0 0 0.3125em 0.3125em;
      width: calc(100% + 2px);
      left: -1px;
      border: $border;
      background: #FFF;
      box-shadow: none;
      max-height: 11.75em;
      overflow-y: auto;
      z-index: 4;
      &::-webkit-scrollbar {
        width: 0.625em;
        height: 0.625em;
      }
      &::-webkit-scrollbar-track {
        background: #F3F6F9;
      }
      &::-webkit-scrollbar-thumb {
        background: rgba(#1B222B,.6);
        border-radius: 5.625em;
        width: 0.375em;
      }
    }
    .option{
      width: calc(100% + 2px);
      height: 3.8125em;
      font-size: 1em;
      line-height: 157%;
      padding: 0 1.07em;
      display: flex;
      align-items: center;
      color: #58595A;
      border: none;
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: #186FD4;
        font-weight: 300;
        color: #fff;
      }
    }
  }
  ._error-text{
    display: none;
  }
  &._error{
    input{
      background-image: url(../img/error.svg);
      background-repeat: no-repeat;
      background-position: right 1.25em center;
      background-size: 1.4375em auto;
    }
    input,
    textarea{
      border-color: #E52B0E;
      &::placeholder{
        color: #E52B0E;
      }
    }
    ._error-text{
      display: block;
      margin-top: .5em;
      color: #E52B0E;
      font-weight: 500;
      font-style: italic;
      span{
        font-size: 0.875em;
      }
    }
  }
}

.pass-toggle{
  position: absolute;
  bottom: 1.3125em;
  right: .94em;
  width: 1.25em;
  height: 1.25em;
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
  &._active{
    path{
      stroke: #186FD4;
    }
    g{
      opacity: 1;
    }
  }
}

.m-radio,
.file-input,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    color: #2C333D;
    span{
      font-size: .9375em;
      line-height: 180%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      transition: all .100s;
      background-color: #fff;
    }
    a{
      text-decoration: underline;
      font-size: .9375em;
      color: #186FD4;
      position: relative;
      z-index: 2;
    }
  }
  &._error{
    input:not(:checked){
      &~label:before{
        border-color: #E52B0E;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: .75em auto;
      }
    }
  }
  label {
    padding-left: 2.44em;
    min-height: 1.6875em;
    color: #363839;
    &:before {
      border-radius: .25em;
      border: 1px solid #DFE0E2;
      width: 1.625em;
      height: 1.625em;
      @include bgImgSize(0);
      background-image: url(../img/check.svg);
      transition: 0;
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border-color: #186FD4;
    }
    &:after{
      opacity: 1;
    }
  }
  label{
    padding-left: 2.375em;
    min-height: 1.4375em;
    &:before{
      width: 1.4375em;
      height: 1.4375em;
      border: $border;
      border-radius: 50%;
    }
    &:after{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 1.4375em;
      height: 1.4375em;
      border-radius: 50%;
      background-image: url(../img/radio3.svg);
      @include bgImgSize(100%);
      transition: all .1s;
      opacity: 0;
      z-index: 2;
    }
  }
}

.file-input{
  input:hover{
    &~.file-input__wrap{
      border-color: $primary;
    }
  }
  &__wrap{
    padding: 1.56em 1.25em;
    border-radius: 0.4375em;
    border: 1px dashed #EBEEF0;
    img{
      width: 1.125em;
      display: block;
      margin: 0 auto .44em;
    }
    .text16{
      color: #ADB1B7;
      text-align: center;
      line-height: 140%;
    }
  }
}
