.cart{
  &__inner{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: calc(100% - 28.0625em - 1.25em) 28.0625em;
    gap: 1.25em;
  }
}

.cart-sale{
  border-radius: 0.5em;
  border: 1px solid #EDF0F2;
  padding: .94em 1.56em;
  &__title,
  &__desc{
    font-weight: 500;
    line-height: 187%;
    span{
      color: #003B73;
      font-weight: 700;
    }
  }
  &__progress{
    margin-top: .625em;
    &-nums{
      margin-top: .3125em;
      display: flex;
      justify-content: space-between;
    }
    &-num{
      font-size: 0.9375em;
      line-height: 187%;
      color: #898989;
      white-space: nowrap;
      &._current{
        color: #3D3D3D;
        text-align: center;
      }
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: .625em;
  }
  &__desc{
    font-weight: 400;
    color: #343A43;
  }
  &__hint{
    margin-left: .625em;
    position: relative;
    &-toggle{
      cursor: pointer;
      @include center;
      padding: 0 1.0625em;
      font-weight: 500;
      height: 2.5625em;
      transition: all .2s;
      span{
        font-size: 0.9375em;
      }
      ._view2{
        display: none;
      }
    }
  }
}

.progress-bar{
  border-radius: 0.5em;
  background: #F3F4F5;
  height: .4375em;
  position: relative;
  span{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    border-radius: inherit;
    background-color: #186FD4;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url(../img/progress.webp);
      background-size: auto 100%;
    }
  }
}

._tooltip-toggler{
  &:hover{
    .tooltip{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.tooltip{
  position: absolute;
  bottom: 99%;
  left: 50%;
  transform: translateX(-50%);
  padding-bottom: 1.19em;
  width: 30.5625em;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &__inner{
    border-radius: 0.375em;
    background: #FFF;
    box-shadow: 0 .25em 1.875em 0 rgba(0, 0, 0, 0.09);
    padding: 1.56em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 1.25em;
      height: 1.25em;
      bottom: 0;
      left: 50%;
      transform: rotate(45deg) translate(-50%,75%);
      background-color: #fff;
    }
  }
  &__title{
    color: #186FD4;
    font-size: 1.125em;
    line-height: 144%;
    font-weight: 700;
  }
  &__items{
    margin-top: .625em;
    display: flex;
    justify-content: space-between;
  }
  &__item{
    display: flex;
    align-items: center;
    color: $black2;
    .m-badge3{
      margin-left: .3125em;
      min-width: 2.375em;
      justify-content: center;
    }
  }
}

.cart-sum{
  position: sticky;
  top: 1em;
  border-radius: 0.375em;
  background: #FFF;
  box-shadow: 0px 4px 19px 0px rgba(23, 27, 32, 0.07);
  padding: 1.56em;
  @include grid100gap(1.56em);
  &__title{
    font-size: 1.75em;
    line-height: 157%;
    font-weight: 500;
  }
  &__btn{
    height: 3.8125em;
  }
  &__promo{
    @include grid100gap(.625em);
    .m-btn-light-blue2{
      span {
        font-size: .9375em;
      }
    }
    .cart-sum__btn{
      ._view2{
        display: none;
      }
    }
    .fg{
      input{
        background-size: .875em auto;
        background-repeat: no-repeat;
        background-position: right 1.25em center;
      }
    }
    &._applied{
      .fg{
        input{
          background-image: url(../img/check-green.svg);
          border-color: #1B9665;
        }
      }
      .cart-sum__btn{
        border: 2px solid #F6F8FB;
        background-color: transparent;
        pointer-events: none;
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
  }
  .prop-item{
    &__desc{
      color: #4D6159;
    }
  }
}

.cart-table{
  margin-top: 2.5em;
  th{
    text-transform: uppercase;
  }
  th,
  td{
    padding: .94em 1.25em;
    &:last-child{
      min-width: 9em;
    }
  }
}

.cart-item{
  &__info{
    display: flex;
    align-items: center;
    max-width: 29.125em;
  }
  &__img{
    flex-shrink: 0;
    width: 5.625em;
    height: 5.625em;
    @include center;
    margin-right: .94em;
    img{
      border-radius: .5em;
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__content{
    max-width: 18em;
    @include grid100gap(.3125em);
  }
  &__title{
    @include truncate(2);
    line-height: 162%;
  }
  &__articul{
    color: #A8ADB2;
    font-size: .8125em;
  }
  &__price1,
  &__price3{
    font-size: 1.25em;
    line-height: 110%;
    color: #003B73;
    white-space: nowrap;
  }
  &__price2{
    font-size: .9375em;
    line-height: 147%;
    color: #8F9296;
    text-decoration: line-through;
  }
  .cartcalc{
    width: 9.9375em;
    max-width: 100%;
  }
  &__action{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__delete{
    padding: .25em;
    width: 1.625em;
    flex-shrink: 0;
    margin-left: 1em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $red;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .cart{
    &__inner{
      grid-template-columns: 100%;
      gap: 1.875em;
    }
  }

  .cart-sale{
    padding: .94em 1.25em;
    &__bottom{
      justify-content: space-between;
    }
    &__desc{
      .text15{
        font-size: .875em;
      }
    }
    &__hint{
      &-toggle{
        padding: 0 .75em;
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
  }

  .cart-sum{
    padding: 1.25em;
    gap: .94em;
  }

  .cart-table{
    margin-top: 1.56em;
    thead{
      display: none;
    }
    th,td{
      padding: 1.25em .25em;
      border-left: none !important;
      border-right: none !important;
      &:first-child{
        padding-left: 0;
      }
      &:nth-child(4){
        padding-right: 0;
        width: 1.625em;
        min-width: auto;
      }
    }
  }

  .cart-item{
    &__info{
      width: 22.125em;
    }
    &__price3{
      display: none;
    }
    &__delete{
      padding: 0;
      width: 1.125em;
      margin-left: auto;
    }
  }
}


@media screen and (max-width: 576px){
  .cart-sale{
    padding: .94em 1.56em;
    &__progress{
      &-num{
        span{
          font-size: .875em;
        }
      }
    }
    &__bottom{
      display: block;
    }
    &__hint{
      width: 100%;
      margin: .625em 0 0;
    }
  }

  .tooltip{
    width: 100%;
    padding-bottom: 1em;
    &__inner{
      padding: .94em .94em 1em;
    }
    &__title{
      font-size: 1em;
      line-height: 130%;
    }
    &__items{
      position: relative;
      z-index: 2;
      @include grid100gap(.75em);
    }
  }

  .cart-table{
    table,
    tbody{
      display: block;
    }
    tr{
      padding: 1.25em 0;
      border-bottom: 1px solid #EDF0F2;
      display: grid;
      grid-template-columns: 9.9375em 6em 1.125em;
      justify-content: space-between;
      align-items: center;
    }
    td{
      width: 100% !important;
      padding: 0;
      border: none;
      &:first-child{
        grid-column: 1/-1;
      }
      &:nth-child(3){
        grid-column: 1/2;
        grid-row: 2/3;
      }
    }
  }
}
