.header-top{
  padding: .5625em 0;
  border-bottom: $border;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.burger{
  display: none;
}

.mob-menu{
  &__top,
  &__sale,
  &__bg{
    display: none;
  }
}

._backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: $fullHeight;
  background: rgba(1, 17, 32, 0.90);
  z-index: 19;
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    margin-right: 1.875em;
    color: #6C6F71;
    span{
      font-size: .875em;
    }
  }
}

.header-contacts{
  display: flex;
  align-items: center;
  &__btn{
    text-transform: uppercase;
    height: 2em;
    padding: 0 .75em;
    span{
      font-size: 0.6875em;
      line-height: 120%;
    }
  }
  &__phone{
    margin-right: .625em;
  }
  &__time{
    color: #4E5760;
    margin-right: 1.25em;
  }
  &._type2{
    display: none;
  }
}

.header-bottom{
  padding: 1.69em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.header-action{
  display: flex;
  align-items: center;
}

.header-btn{
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  color: #6B7076;
  position: relative;
  @include lastM(0 1.6875em 0 0);
  img{
    width: auto;
    height: 1.5625em;
    margin-bottom: .75em;
  }
  &__title{
    font-size: 0.875em;
    line-height: 58%;
  }
  &__pin{
    position: absolute;
    top: -.375em;
    left: 50%;
    transform: translateX(50%);
    font-weight: 500;
    background-color: #E52B0E;
    color: #fff;
    min-width: 1.0625em;
    height: 1.0625em;
    padding: 0 .125em;
    border-radius: 50%;
    @include center;
    span{
      font-size: 0.625em;
      line-height: 120%;
    }
  }
}

.header-action2{
  margin: 0 2.33em;
  flex-grow: 1;
  display: flex;
}

.catalog-btn{
  margin-right: 2.33em;
  flex-shrink: 0;
  &__icon{
    width: 1.25em;
    height: 1.25em;
    flex-shrink: 0;
    margin-right: 1em;
    position: relative;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
      transition: all .3s;
    }
    ._view2{
      opacity: 0;
    }
  }
}

.header-search{
  flex-grow: 1;
  position: relative;
  &._show-dropdown{
    .header-search__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  .search-field{
    width: 100%;
    position: relative;
    z-index: 2;
  }
  &__dropdown{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: absolute;
    border-radius: 0.5em;
    background: #FFF;
    box-shadow: 0 .25em 2.25em 0 rgba(0, 0, 0, 0.15);
    width: 100%;
    top: 100%;
    right: 0;
    margin-top: -2em;
    padding-top: 2em;
  }
  &__items{
    max-height: 23.3em;
    overflow-y: auto;
    @include scroll;
  }
  &__link{
    border-radius: 0 0 0.5em 0.5em;
    background: #E7F1FC;
    @include center;
    height: 2.625em;
    color: #186FD4;
    font-weight: 500;
    &:hover{
      background: darken(#E7F1FC,10%);
      color: #186FD4;
    }
    span{
      font-size: .9375em;
      line-height: 120%;
    }
  }
}

.search-card{
  display: grid;
  grid-template-columns: 5.375em calc(100% - 7.0625em - 5.375em - 1.875em) 7.0625em;
  gap: .9375em;
  padding: 1.125em 1.25em;
  align-items: flex-start;
  &:not(:last-child){
    border-bottom: 1px solid #EBEEF0;
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: 0.1875em;
    border: 1px solid #C7CBD2;
    @include imgCover(81.3%);
  }
  &__content{
    @include grid100gap(.5em);
    .text13{
      color: #A8ADB2;
    }
  }
  &__badges{
    display: flex;
  }
  .m-badge{
    background: #F3F4F5;
    color: #354F6A;
    height: 1.8125em;
    padding: 0 .375em;
    span{
      font-size: .8125em;
    }
  }
  &__nav{
    @include center;
    align-self: center;
  }
  .arrow-link{
    display: flex;
    align-items: center;
    color: #186FD4;
    font-weight: 500;
    &:hover{
      color: $black;
      path{
        fill: $black;
      }
    }
    span{
      font-size: .9375em;
      line-height: 173%;
    }
    svg{
      width: .5em;
      height: auto;
      margin-left: .25em;
    }
  }
}

.search-field{
  flex-grow: 1;
  background-color: $primary;
  border-radius: 0.5em;
  height: 3.25em;
  padding: .1875em;
  display: flex;
  align-items: center;
  input{
    border: none;
    width: 100%;
    height: 100%;
    font-size: 0.875em;
    color: #A0A3A5;
    border-radius: .43em;
    padding: 0 1.07em;
    &::placeholder{
      color: #A0A3A5;
    }
  }
  &__btn{
    width: 3.5em;
    flex-shrink: 0;
    @include center;
    &:hover{
      img{
        transform: scale(1.1);
      }
    }
    img{
      width: 1em;
      transition: all .3s;
    }
  }
}

.header{
  position: relative;
  z-index: 10;
  &__logo{
    display: block;
    width: 13.4375em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &._open-catalog-menu{
    .catalog-btn{
      background: #011120;
      &__icon{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &._open-menu{
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .mob-menu{
        transform: translateX(0);
        &__bg{
          display: block;
        }
      }
    }
  }

  .header-top{
    padding: .375em 0;
  }

  .burger{
    display: flex;
    align-items: center;
    color: #2E3032;
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .81em;
    }
  }

  .mob-menu{
    position: fixed;
    width: 20.6875em;
    max-width: 100%;
    height: $fullHeight;
    background: #FFF;
    left: 0;
    top: 0;
    z-index: 20;
    overflow-y: auto;
    padding-bottom: 2em;
    transform: translateX(-150%);
    transition: all .3s;
    &__top{
      padding: 1.56em .625em .625em 1.56em;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: $border;
    }
    &__close{
      width: 1.625em;
      height: 1.625em;
      @include center;
      svg{
        width: .5em;
        height: auto;
      }
      &:hover{
        path{
          stroke: #fff;
        }
      }
    }
  }

  .mob-menu{
    &__sale{
      display: flex;
      align-items: center;
      height: 3.5625em;
      border-bottom: $border;
      padding: 0 1.5625em;
      font-weight: 500;
      text-transform: uppercase;
      img{
        width: 1.3125em;
        flex-shrink: 0;
        margin-right: .625em;
      }
    }
  }

  .nav{
    display: block;
    &__link{
      height: 3.0625em;
      display: flex;
      align-items: center;
      padding: .5em 1.5625em;
      border-bottom: $border;
      margin: 0;
      color: #2E3032;
      text-transform: uppercase;
      font-weight: 500;
      span{
        font-size: .8125em;
      }
    }
  }

  .header-contacts{
    &._type2{
      padding: 0 1em;
      margin-top: 1.875em;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      .header-contacts__time{
        order: 2;
        margin: .81em 0 0;
      }
      .header-contacts__phone{
        margin-right: .81em;
      }
      .header-contacts__btn{
        padding: 0 1em;
      }
    }
  }

  .header-bottom{
    padding: .94em 0;
    &__inner{
      flex-wrap: wrap;
    }
  }

  .header-action2{
    order: 2;
    width: 100%;
    margin: .94em 0 0;
  }

  .catalog-btn{
    margin-right: .94em;
  }
}


@media screen and (max-width: 576px){
  .burger{
    span{
      display: none;
    }
  }

  .header-contacts{
    &._type1{
      .header-contacts__time{
        display: none;
      }
    }
  }

  .header{
    &__logo{
      width: 9.9375em;
    }
  }

  .header-action{

  }

  .header-btn{
    @include lastM(0 .375em 0 0);
    &._hide-mob{
      display: none;
    }
    &__title{
      display: none;
    }
    img{
      margin: 0;
    }
    &__pin{
      left: unset;
      transform: unset;
      right: -.125em;
    }
  }

  .catalog-btn{
    padding: 0 1.31em;
    margin-right: .75em;
    height: 2.875em;
  }

  .search-field{
    height: 2.875em;
    padding: .125em;
    &__btn{
      width: 2.375em;
    }
  }

  .header-search{
    &__dropdown{
      margin-top: .25em;
      padding-top: 0;
      width: calc(100vw - 1.875em);
    }
  }

  .search-card{
    padding: .5em;
    grid-template-columns: 100%;
    gap: .5em;
    &__img{
      max-width: 5em;
      padding-bottom: unset;
      img{
        position: static;
        height: auto;
      }
    }
    &__nav{
      justify-content: unset;
    }
  }
}
