.main{
  overflow: hidden;
}

.main-slider{
  position: relative;
  .swiper{
    &-container{
      overflow: hidden;
      border-radius: 0.625em;
    }
    &-pagination{
      bottom: 1.875em;
      z-index: 1;
      &-bullet{
        width: 1.0625em;
        height: 1.0625em;
        margin: 0 .47em !important;
        flex-shrink: 0;
        opacity: 1;
        border: 3px solid rgba(255, 255, 255, 0.70);
        background-color: transparent;
        transition: all .3s;
        &:hover,
        &-active{
          border-color: #117FE3;
        }
      }
    }
    &-button{
      &-prev{
        left: -2.19em;
      }
      &-next{
        right: -2.19em;
      }
    }
  }
}

.main-banner{
  padding: 7.625em 3.75em;
  border-radius: 0.625em;
  height: auto;
  background-size: cover !important;
  background-position: center !important;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      display: block;
      border-radius: .625em;
      object-fit: cover;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__content{
    max-width: 31em;
    position: relative;
    z-index: 2;
  }
  &__text{
    @include grid100gap(.875em);
  }
  &__nav{
    display: flex;
    margin-top: 1.44em;
  }
  @media screen and (min-width: 577px){
    .main-banner__btn{
      padding-left: 3em;
      img{
        margin-left: 2em;
      }
    }
  }

}

.advantages{
  margin-top: 3.125em;
  display: flex;
  align-items: center;
}

.advantage-item{
  display: flex;
  align-items: center;
  &__icon{
    width: 1.3125em;
    height: 1.125em;
    flex-shrink: 0;
    margin-right: .69em;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  @media screen and (min-width: 993px){
    &:not(:last-child){
      flex-grow: 1;
      margin-right: 4.8em;
      &:after{
        content: '';
        width: 1px;
        height: 1.4375em;
        margin-left: auto;
        background: #E7E7E8;
        flex-shrink: 0;
      }
    }
  }
}

.main-catalog{
  margin-top: 3.125em;

  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(8,calc(12.5% - 1.25em * 7 / 8));
        gap: 1.25em;
        transform: unset !important;
      }
    }
  }
}

.category-card{
  border-radius: 0.9375rem;
  background: #F3F4F5;
  padding-bottom: 1.81em;
  display: block;
  color: $black3;
  height: 100%;
  &._link{
    border: 2px solid #F6F7F9;
    background-color: transparent;
    &:hover{
      circle{
        fill: $black5,
      }
      path{
        fill: #fff,
      }
    }
  }
  &__img{
    height: 7.94em;
    padding: 1.25em .875em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
      mix-blend-mode: multiply;
    }
    svg{
      width: 3.6875em;
      height: auto;
    }
  }
  &__title{
    text-align: center;
    @include center;
    min-height: 2.5625em;
    padding: 0 .5em;
  }
}

.main-promos{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
  gap: 1.25em;
  margin-top: 3.125em;
}

.promo-card{
  position: relative;
  &__img{
    border-radius: .5em;
    @include imgCover(45.1%);
  }
  &__content{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 1em 2.25em;
    max-width: 13em;
    z-index: 2;
  }
  &__badge{
    margin-top: .44em;
  }
}

.m-badge{
  flex-shrink: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 .5em;
  height: 1.9375em;
  border-radius: 0.375em;
  background-color: $black5;
  color: #fff;
  font-weight: 700;
  span{
    font-size: 0.875em;
    line-height: 135%;
  }
}


@media screen and (max-width: 992px){
  .main-slider{
    .swiper{
      &-button{
        &-prev{
          left: -.625em;
        }
        &-next{
          right: -.625em;
        }
      }
      &-pagination{
        bottom: 1.56em;
        &-bullet{
          width: 0.8125em;
          height: 0.8125em;
        }
      }
    }
  }

  .main-banner{
    padding: 4em 1.875em;
    &__img{
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
    }
    &__content{
      max-width: 26.9375em;
    }
    &__text{
      gap: .5em;
    }
    &__nav{
      margin-top: .94em;
    }
  }

  .advantages{
    margin-top: 1.875em;
    align-items: unset;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.25em;
  }

  .advantage-item{
    align-items: flex-start;
    &__icon{
      margin-right: .375em;
      width: 1.1875em;
    }
    .text15{
      font-weight: 400;
    }
  }

  .main-catalog{
    margin-top: 1.875em;
    @media screen and (min-width: 577px){
      .swiper-slide{
        width: 10.7em;
        padding-right: .95em;
      }
    }
  }

  .category-card{
    padding-bottom: 1em;
    &__img{
      height: 6.75em;
      svg{
        width: 3.10944em;
      }
    }
  }

  .main-promos{
    grid-template-columns: repeat(3,calc(100% / 3 - 1.88em  / 3));
    gap: .94em;
    margin-top: 1.875em;
    @media screen and (min-width: 577px){
      .promo-card:nth-child(3)~.promo-card{
        display: none;
      }
    }
  }

  .promo-card{
    &__content{
      padding: .875em;
      max-width: 8.5em;
    }
    .m-badge{
      margin-top: .31em;
    }
  }

  .m-badge{
    padding: 0 .375em;
    height: 1.5625em;
    span{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-banner{
    padding: 3.38em .94em;
    min-height: 28.1875em;
    display: flex;
    align-items: flex-end;
    text-align: center;
    &__img{
      ._view3{
        display: block;
      }
      ._view2{
        display: none;
      }
    }
    .text17{
      display: none;
    }
    &__nav{
      margin-top: .69em;
      justify-content: center;
    }
    &__btn{
      padding: 0 1.31em;
      height: 3.3125em;
      span{
        font-size: 0.75em;
      }
    }
  }

  .advantages{
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: 1.875em .5em;
  }

  .main-catalog{
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .47em));
        gap: .94em;
        transform: unset !important;
      }
    }
  }

  .main-promos{
    display: flex;
    flex-wrap: nowrap;
    gap: unset;
    overflow-x: auto;
    @include fullScreenMob;
    @include nullScroll;
    .promo-card{
      width: 14.375em;
      @include lastM(0 .94em 0 0);
    }
  }

  .promo-card{
    flex-shrink: 0;
  }
}
