.compare{
  overflow: hidden;
  &__grid{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: 18.625em calc(100% - 18.625em);
    position: relative;
  }
  &__sidebar{
    padding-top: 23em;
    background-color: #fff;
    position: relative;
    z-index: 3;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      right: 100%;
      background-color: #fff;
      height: 100%;
      width: 1000px;
    }
  }
}

.compare-row{
  padding: .625em 0 .625em .625em;
  height: 3.25em;
  display: flex;
  align-items: center;
  border-top: 1px solid #EFF3F9;
  &:nth-child(even){
    background: #F9F9FB;
  }
  &__title{
    font-weight: 500;
    color: $black2;
  }
  &__desc{
    color: #3E4247;
  }
  &__title,
  &__desc{
    font-size: .9375em;
    line-height: 147%;
  }
}

.compare-slider{
  width: calc(19.25em * 4);
  max-width: 100%;
  .swiper{
    &-slide{
      width: 19.25em;
      transition: all .3s;
      &:last-child{
        .compare-item{
          max-width: 18em;
          .compare-card{
            padding-right: 0;
          }
        }
      }
      @media screen and (min-width: 993px){
        opacity: 0;
        pointer-events: none;
        &-active,
        &-active ~ .swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &-scrollbar{
      display: none;
    }
  }
  .compare-row{
    padding-left: 1.875em;
  }
}

.swiper-scrollbar{
  margin-top: 3.125em;
  height: 0.3125em;
  background: #D9D9D9;
  &-drag{
    background: #186FD4;
  }
}

.swiper-scrollbar,
.swiper-scrollbar-drag{
  position: relative;
  &:before{
    content: '';
    position: absolute;
    height: 300%;
    width: 100%;
    transform: translateY(-50%);
    left: 0;
    top: 50%;
  }
}

.compare-card{
  padding: 0 1.25em;
  max-width: 19.25em;
  height: 23em;
  position: relative;
  &__delete{
    position: absolute;
    top: 0;
    right: 0;
    width: 1.875em;
    height: 1.875em;
    border-radius: 0.25em;
    background: #F9F9FB;
    @include center;
    svg{
      width: 1em;
      height: auto;
    }
    &:hover{
      path{
        stroke: $red;
      }
    }
  }
  &__img{
    margin-bottom: .625em;
    height: 12.625em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  .product-card__nav{
    display: grid;
    grid-template-columns: calc(100% - 3.125em) 2.5em;
    gap: .625em;
  }
  .cart-btn{
    width: 100%;
  }
  &__title{
    @include truncate(2);
  }
}

.compare-item{
  @media screen and (min-width: 993px){
    .compare-row{
      &__title{
        display: none;
      }
    }
  }
}

.compare-nav{
  position: absolute;
  top: 7.7em;
  left: 0;
  width: 18.625em;
  z-index: 4;
  &__select{
    max-width: 17.5625em;
  }
  &__radios{
    margin-top: 1.78em;
    @include grid100gap(1.25em);
  }
}


@media screen and (max-width: 992px){
  .compare{
    &__grid{
      margin-top: .44em;
      grid-template-columns: 100%;
    }
    &__sidebar{
      display: none;
    }
  }

  .compare-slider{
    .swiper{
      &-slide{
        width: 16.8125em;
        padding-right: .625em;
      }
      &-scrollbar{
        display: block;
      }
    }

    .compare-row{
      padding: .625em;
      @include grid100gap(.3125em);
      &__title{
        display: block;
      }
    }
  }

  .compare-row{
    height: auto;
  }

  .compare-nav{
    position: static;
    margin: 0 0 2.5em;
    width: 100%;
    @media screen and (min-width: 577px){
      display: flex;
      align-items: center;
      justify-content: space-between;
      &__radios{
        display: flex;
        align-items: center;
        gap: unset;
        margin: 0;
        .m-radio{
          margin-left: .94em;
        }
      }
    }
    &__select{
      width: 20.625em;
      max-width: 100%;
    }
  }
}



@media screen and (max-width: 576px){
  .compare{
    &__grid{
      margin-top: 1.25em;
    }
  }

  .compare-slider{
    .swiper{
      &-scrollbar{
        margin-top: 1.875em;
      }
    }
  }

  .compare-nav{
    margin: 0 0 1.875em;
    &__radios{
      gap: .9375em;
      margin-top: 1.25em;
    }
  }
}
