.header._open-catalog-menu{
  .menu{
    opacity: 1;
    pointer-events: unset;
    transform: translateX(0);
    @media screen and (max-width: 992px){
      &__bg{
        display: block;
      }
    }
  }
}

.header._open-brand-menu{
  .brand-menu{
    opacity: 1;
    pointer-events: unset;
    transform: translateX(0);
  }
}

.header._open-sale-menu{
  .sale-menu{
    opacity: 1;
    pointer-events: unset;
    transform: translateX(0);
  }
}

.menu{
  margin-top: -1px;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  z-index: 10;
  background: #F9FAFB;
  box-shadow: 0px 9px 15px 0px rgba(0, 0, 0, 0.09);
  padding: 1.56em 0;
  &__bg{
    display: none;
  }
}

.menu-level{
  border-radius: 0.5em;
  background: #FFF;
  width: 21.625em;
  overflow: hidden;
}

.menu{
  &__items{
    position: relative;
  }
  &__banner{
    width: 20.0625em;
    max-width: 100%;
    padding: 2.4em 0;
    .sale-card{
      color: #07353A;
      &__img{
        padding-bottom: 139%;
      }
    }
  }
}

.menu-item{
  &:last-child{
    .menu-item__show{
      border-bottom: none;
    }
  }
  @media screen and (min-width: 993px){
    &:hover{
      .menu-item__show{
        background-color: $primary;
        color: #fff;
      }
      .menu-item__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__show:hover,
    &:hover .menu-item__show{
      .menu-item__icon{
        path{
          stroke: #fff;
        }
      }
      .menu-item__arrow{
        path{
          fill: #fff;
        }
      }
      .menu-item__num{
        color: rgba(#fff,.7);
      }
    }
  }
  &__show{
    padding: .94em 1.56em;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #EBF2F6;
    min-height: 3.625em;
    transition: all .2s;
    cursor: pointer;
    .text13{
      text-transform: uppercase;
      font-weight: 500;
      line-height: 154%;
    }
  }
  &__arrow{
    margin-left: auto;
    width: .3125em;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__hidden{
    min-height: 100%;
    position: absolute;
    top: 0;
    left: 21.625em;
    margin-left: -1px;
    width: 26.5625em;
    opacity: 0;
    pointer-events: none;
    transition: all .2s;
    padding-left: 1.875em;
  }
}

.menu2{
  border-radius: 0.5em;
  background: #FFF;
}

.menu-item2{
  @media screen and (min-width: 993px){
    &:hover{
      .menu-item2__hidden{
        opacity: 1;
        pointer-events: unset;
      }
      .menu-item__show{
        background-color: $primary;
      }
    }
    &:hover .menu-item2__show,
    &__show:hover{
      color: $primary;
      .menu-item__arrow{
        path{
          fill: $primary;
        }
      }
    }
  }
  &__show{
    display: flex;
    align-items: center;
    cursor: pointer;
    min-height: 3.875em;
    color: #2C3033;
    padding: 0 1.56em;
  }
  &__title{
    font-size: 1em;
    line-height: 130%;
    font-weight: 500;
  }
  &__hidden{
    position: absolute;
    top: 0;
    left: 26.5625em;
    margin-left: -1px;
    width: 46.875em;
    opacity: 0;
    pointer-events: none;
    transition: all .2s;
    padding: .94em 1.875em;
  }
}

.menu3{
  &__links{
    column-count: 2;
    column-gap: 1.875em;
  }
  &__link{
    color: #26292D;
    width: 100%;
    display: inline-block;
    @include lastM(0 0 1em);
    span{
      font-size: 1em;
      line-height: 157%;
    }
  }

}

.menu-top{
  display: none;
  &__close,
  &__back{
    position: relative;
    flex-shrink: 0;
  }
  &__title{
    font-size: 1.1875em;
    line-height: 100%;
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){
  .menu{
    position: fixed;
    width: 20.6875em;
    max-width: 100%;
    height: $fullHeight;
    background: #FFF;
    left: 0;
    top: 0;
    z-index: 20;
    overflow-y: auto;
    padding-bottom: 2em;
    transform: translateX(-150%);
    transition: all .3s;
    &-top{
      padding: 1.56em .625em .625em 1.56em;
      display: flex;
      align-items: center;
      &__close{
        margin-left: auto;
        width: 1.625em;
        height: 1.625em;
        @include center;
        svg{
          width: .5em;
          height: auto;
        }
        &:hover{
          path{
            stroke: #fff;
          }
        }
      }
    }
  }

  .menu-item2__hidden,
  .menu-item__hidden,
  .menu{
    overflow: hidden;
    height: $fullHeight;
    top: 0;
    left: 0;
    position: fixed;
  }

  .menu{
    padding: 0 0 2em;
    opacity: 1;
    pointer-events: unset;
    transform: translateX(-120%);
    .container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__banner{
      display: none;
    }
  }

  .menu-top{
    padding: 1.56em .625em .625em 1.56em;
    &__title{
      padding-right: .5em;
    }
    &__back{
      width: .875em;
      margin-right: .625em;
      flex-shrink: 0;
      svg{
        width: 100%;
        display: block;
        height: auto;
      }
    }
  }

  .menu-scroll{
    overflow-y: auto;
    height: calc(100 * var(--vh) - 3.875em);
  }

  .menu-level{
    width: 100%;
  }

  .menu-item{
    width: 100%;
    &._open-menu2{
      .menu-item__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__hidden{
      background-color: #fff;
      width: 100%;
      padding: 0;
    }
  }

  .menu2{
    .menu-scroll{
      padding-top: .625em;
    }
  }

  .menu-item2{
    &._open-menu3{
      .menu-item2__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__show{
    }
  }

  .menu-item2{
    width: 100%;
    &__hidden{
      background-color: #fff;
      width: 100%;
      padding: 0;
    }
  }

  .menu3{
    &__title{
      display: none;
    }
    &__links{
      margin: 0;
      column-count: 1;
      padding: .625em 1.56em;
    }
  }

  .sale-menu,
  .brand-menu{
    display: none;
  }
}
